import React, {useState} from "react";
import {Box, Grid, makeStyles, Paper, Typography} from "@material-ui/core";
import {MasInput} from "../../components/ui/MasInput";
import MasDataTable from "../../components/data-table/index";
import {MasButton} from "../../components/ui/MasButton";
import {InputText} from "primereact/inputtext";
import {ColumnProps} from "primereact/column";

export default function CancellBillsRequest() {
	const classes = useStyles();
	const [items, setItems] = useState([]);
	const [selected, setSelected] = useState([]);

	const renderHeader = (
		<div className="table-header">
			<Typography variant="h6" className="bold">
				 
			</Typography>
			<Typography className={classes.selectionInfo} variant="body1">
				  = <>{selected.length}</>
			</Typography>
		</div>
	);

	const onEditorValueChange = (props: any, val: string) => {
		let updatedProducts = [...props.value];
		updatedProducts[props.rowIndex][props.field] = val;
		items.map((item: any, idx: number) => {
			if (idx === props.rowIndex) {
				item[props.field] = val;
				return item;
			}
			return <></>;
		});
		setItems([...items]);
	};

	const commentEditor = (props: any) => {
		return (
			<InputText
				type="text"
				style={{width: "100%"}}
				id={props.rowData["id"]}
				value={props.rowData["image"]}
				onChange={(e) => onEditorValueChange(props, e.target.value)}
			/>
		);
	};

	const commentTemplete = (rowData: any) => {
		return <InputText style={{width: "100%"}} type="text" value={rowData["image"]} />;
	};

	const columns: ColumnProps[] = [
		{
			selectionMode: "multiple",
			headerStyle: {width: "3rem"},
		},
		{
			field: "name",
			header: " ",
		},
		{
			field: "code",
			header: " ",
		},
		{field: "category", header: " "},
		{field: "quantity", header: " "},
		{
			field: "image",
			header: "",
			headerStyle: {width: "30%"},
			body: commentTemplete,
			editor: commentEditor,
		},
	];

	return (
		<Grid container justify="center" direction="column" className={classes.wrapper}>
			<Grid alignItems="center" container className={classes.paegHeader}>
				<Box className={classes.circle}>
					{/* <CashRemove className={classes.iconHeader} /> */}
				</Box>
				<Typography variant="h5">     :</Typography>
			</Grid>
			<Paper className={classes.content}>
				<Grid
					spacing={1}
					container
					direction="row"
					alignItems="center"
					justify="space-between"
				>
					<Grid item className="flexgrow">
						<MasInput placeholder="    " />
					</Grid>
					<Grid item>
						<MasButton
							type="submit"
							label="  "
							color="secondary"
							variant="contained"
							disableElevation
						/>
					</Grid>
				</Grid>
			</Paper>
			<Paper className={classes.orderInfo}>
				<Box className={classes.orderInfo_title}>
					<Typography variant="h6" style={{fontFamily: "DINBLD"}}>
						 
					</Typography>
				</Box>
				<Grid container spacing={1}>
					<Grid item xs={6}>
						<MasInput type="number" label=" " />
					</Grid>
					<Grid item xs={6}>
						<MasInput label="" />
					</Grid>
					<Grid item xs={6}>
						<MasInput type="date" label=" " />
					</Grid>
					<Grid item xs={6}>
						<MasInput label=" " type="date" />
					</Grid>
				</Grid>
			</Paper>
			<Paper className={classes.dataTable}>
				<MasDataTable
					selection={selected}
					onSelectionChange={(e) => setSelected(e.value)}
					header={renderHeader}
					data={products}
					columns={columns}
				/>
			</Paper>

			<Box className={classes.saveBtn}>
				<MasButton type="submit" label=" " color="primary" variant="contained" />
			</Box>
		</Grid>
	);
}

