import {LoadingTypes, SHOW_LOADING, CLOSE_LOADING} from "../actions/common/loading";
interface ILoading {
	show?: boolean;
}
const LoadingState = {
	show: false,
};
export const Loading = (state: ILoading = LoadingState, action: LoadingTypes) => {
	switch (action.type) {
		case SHOW_LOADING:
			return {
				show: true,
			};
		case CLOSE_LOADING:
			return {
				show: false,
			};
		default:
			return state;
	}
};
