import {AlertProps} from "@material-ui/lab";
import {
	NotificationTypes,
	SHOW_NOTIFICATION,
	CLOSE_NOTIFICATION,
} from "../actions/common/notification";

interface Istate {
	message?: string;
	type?: AlertProps["severity"];
	count: number;
}
const InstailState = {
	count: 0,
};

export const NotificationReducer = (
	state: Istate = InstailState,
	action: NotificationTypes
) => {
	switch (action.type) {
		case SHOW_NOTIFICATION:
			console.log(action, "NOTIFICATION Reducer");
			return {
				message: action.payload.msg,
				type: action.payload.severity,
				count: state.count + 1,
			};
		case CLOSE_NOTIFICATION:
			return {
				count: 0,
			};
		default:
			return state;
	}
};
