import {
	PokemonDispatchTypes,
	PokemonTypes,
	POKEMMON_SUCCESS,
	POKEMMON_FAIL,
	POKEMMON_LOADING,
} from "../actions/pokemon/pokemonTypes";

export interface defaultStateI {
	loading?: boolean;
	pokemon?: PokemonTypes;
	error?: string;
}

const dState = {};

export const PokemonReducser = (
	state: defaultStateI = dState,
	action: PokemonDispatchTypes
) => {
	switch (action.type) {
		case POKEMMON_FAIL:
			return {
				loading: false,
				error: action.payload,
			};
		case POKEMMON_LOADING:
			return {
				loading: true,
			};
		case POKEMMON_SUCCESS:
			return {
				loading: false,
				pokemon: action.payload,
			};

		default:
			return state;
	}
};
