import * as service from "../proto/service_pb_service";
import {
	BillResponce,
	ChargeRequest,
	Ctg,
	Empty,
	Entity,
	ENTITY_TYPEMap,
	GetBillRequest,
	LoginRequest,
	LoginResponce,
	LookUp,
	PostMessage,
} from "../proto/service_pb";
import {dateToTimeStamp, fromChargeRequest, fromPostMessage} from "./converter";
import {showLoading, hideLoading, showNotification} from "../redux/store";

var _GRPCClinet: service.EngineClient;
var defaultMeta = {timeout: 10000, authorization: ""};
const port = 25566;

const Meta = (timeout?: number) => {
	let token = localStorage.getItem("token");
	let intialMeta = {
		timeout: timeout || 1000,
		authorization: "",
	};
	if (token !== null) {
		let auth = "bearer " + token;
		intialMeta.authorization = auth;
		return intialMeta;
	} else {
		if (intialMeta.authorization === "") {
		}
		return intialMeta;
	}
};

export const GetGrpcClient = function (): Promise<service.EngineClient> {
	return new Promise(async (resolve, reject) => {
		try {
			if (_GRPCClinet != null) {
				resolve(_GRPCClinet);
				return;
			}
			let host = window.location.hostname;
			_GRPCClinet = new service.EngineClient(`http://${host}:${port}`, {debug: false});
			// _GRPCClinet = new service.MobileServiceClient(`http://${setting.primary_ip}:25500`, {debug:false, transport: CustomWebsocketTransport() });
			resolve(_GRPCClinet);
		} catch (err) {
			reject(err);
		}
	});
};

export async function Ping(): Promise<string> {
	return new Promise<string>(async (resolve, reject) => {
		try {
			const req = new Empty();
			const client = await GetGrpcClient();
			const customerMeta: any = Meta(5000);
			client.info(req, customerMeta, (err, resp) => {
				if (err != null) {
					reject(err);
				} else {
					resolve(resp.getVersion());
				}
			});
		} catch (ex) {
			reject(ex);
		}
	});
}

export async function GetLookup(
	etype: ENTITY_TYPEMap[keyof ENTITY_TYPEMap]
): Promise<Array<LookUp.AsObject>> {
	return new Promise<Array<LookUp.AsObject>>(async (resolve, reject) => {
		try {
			const req = new Entity();
			req.setEntitytype(etype);
			const client = await GetGrpcClient();
			const customerMeta: any = Meta(5000);
			client.getLoockup(req, customerMeta, (err, resp) => {
				if (err != null) {
					reject(err);
				} else {
					let lst = resp.getLookupsList().map((a) => a.toObject());
					resolve(lst);
				}
			});
		} catch (ex) {
			reject(ex);
		}
	});
}

