export async function GetBillsByCustkey(
	custkey: string,
	bilngDateFrom?: Date,
	bilngDateTo?: Date
): Promise<BillResponce.AsObject> {
	return new Promise<BillResponce.AsObject>(async (resolve, reject) => {
		try {
			showLoading();
			const req = new GetBillRequest();
			req.setCustkey(custkey);
			req.setBilngdatefrom(dateToTimeStamp(bilngDateFrom, true));
			req.setBilngdateto(dateToTimeStamp(bilngDateTo, true));
			const client = await GetGrpcClient();
			const customerMeta: any = Meta(5000);
			client.getBillsByCustkey(req, customerMeta, (err, resp) => {
				if (err != null) {
					reject(err);
					showNotification(err.message, "error");
					hideLoading();
				} else {
					let obj = resp.toObject();
					resolve(obj);
					hideLoading();
				}
			});
		} catch (e) {
			let err = e.Message || e.message || e.error || e.Exception || JSON.stringify(e);
			reject(err);
			showNotification(err, "error");
		}
	});
}

