export async function Charge(
	_req: ChargeRequest.AsObject
): Promise<BillResponce.AsObject> {
	return new Promise<BillResponce.AsObject>(async (resolve, reject) => {
		try {
			showLoading();
			let req = fromChargeRequest(_req);
			const customerMeta: any = Meta(10000);
			const client = await GetGrpcClient();
			client.calulate(req, customerMeta, (err, resp) => {
				if (err != null) {
					reject(err);
					hideLoading();
				} else {
					console.log(resp.toObject(), "response Charge");
					resolve(resp.toObject());
					hideLoading();
				}
			});
		} catch (ex) {
			reject(ex);
		}
	});
}

export async function Post(msg: PostMessage.AsObject): Promise<boolean> {
	return new Promise<boolean>(async (resolve, reject) => {
		try {
			showLoading();
			let req = fromPostMessage(msg);
			const client = await GetGrpcClient();
			const customerMeta: any = Meta(30000);
			client.post(req, customerMeta, (err, resp) => {
				if (err != null) {
					reject(err);
					hideLoading();
				} else {
					resolve(true);
					hideLoading();
				}
			});
		} catch (ex) {
			reject(ex);
		}
	});
}

export async function Login(
	_req: LoginRequest.AsObject
): Promise<LoginResponce.AsObject> {
	return new Promise<LoginResponce.AsObject>(async (resolve, reject) => {
		try {
			let req = new LoginRequest();
			req.setUsername(_req.username);
			req.setPassword(_req.password);
			const customerMeta: any = Meta(30000);
			const client = await GetGrpcClient();
			client.login(req, customerMeta, (err, resp) => {
				if (err != null) {
					reject(err);
				} else {
					let ls = resp.toObject();
					localStorage.setItem("token", ls.token);
					let token = localStorage.getItem("token");
					defaultMeta.authorization = "bearer " + token;
					resolve(ls);
				}
			});
		} catch (ex) {
			reject(ex);
		}
	});
}
