import {Dispatch} from "redux";
import {LoginRequest} from "../../../proto/service_pb";
import {LOGIN_FAIL, LOGIN_SUCCESS, LOG_OUT} from "./loginTypes";
import {Login} from "../../../client/grpc-client";
import {ShowNotification} from "../common/notification";
import {history} from "../../../App";

import {showLoading, hideLoading} from "../../store";

export function NewLogin(loginReq: LoginRequest.AsObject) {
	return async (dispatch: Dispatch) => {
		try {
			showLoading();
			let res = await Login(loginReq);
			dispatch({
				type: LOGIN_SUCCESS,
				payload: res,
			});
			hideLoading();
		} catch (e) {
			let err = e.Message || e.message || e.error || e.Exception || JSON.stringify(e);
			dispatch({
				type: LOGIN_FAIL,
				payload: err,
			});
			hideLoading();
			dispatch(
				ShowNotification({
					msg: err,
					severity: "error",
				})
			);
		}
	};
}

export function SignOut() {
	return (dispatch: Dispatch) => {
		localStorage.removeItem("token");
		dispatch({type: LOG_OUT});
		history.push("/");
	};
}

