import {createMuiTheme} from "@material-ui/core/styles";

const primary = "#007D7D", // 006666
	secondary = "#FF836B",
	third = "#FFBC00",
	primaryLight = "#DDF3F3",
	secondaryLight = "#FFCFC6",
	primaryDrak = "#006666",
	white = "#ffffff",
	background = "#0d2e54",
	textBlack = "#191B1B",
	textLight = "#889797",
	icon = "#758B8B";

declare module "@material-ui/core/styles/createPalette" {
	interface Palette {
		third: Palette["primary"];
		icon: Palette["primary"];
	}
	interface PaletteOptions {
		third: PaletteOptions["primary"];
		icon: PaletteOptions["primary"];
	}
}

declare module "@material-ui/core/styles/createMuiTheme" {
	interface Theme {
		m_shdows: Array<React.CSSProperties["boxShadow"]>;
	}
	interface ThemeOptions {
		m_shdows: Array<React.CSSProperties["boxShadow"]>;
	}
}

const theme = createMuiTheme({
	palette: {
		primary: {
			main: primary,
			contrastText: white,
			light: primaryLight,
			dark: primaryDrak,
		},
		secondary: {
			main: secondary,
			contrastText: white,
			light: secondaryLight,
		},
		text: {
			primary: textBlack,
			secondary: textLight,
		},
		background: {
			default: background,
		},
		third: {
			main: third,
			contrastText: textBlack,
		},
		icon: {
			main: icon,
		},
	},
	m_shdows: [
		"0 1px 12px 0 rgba(46, 61, 73, 0.15)", //0
		"0 1px 20px 0 rgba(46, 61, 73, 0.2)", //1
	],
	typography: {
		fontFamily: "inherit",
	},
	direction: "rtl",
	overrides: {
		MuiCssBaseline: {
			"@global": {
				html: {
					WebkitFontSmoothing: "auto",
				},
				body: {
					backgroundColor: "#fff",
					fontFamily: "DINREG ,  Helvetica, Arial, sans-serif",
				},
			},
		},
		MuiInputBase: {
			root: {
				fontSize: "1.2rem",
				"@media (min-width: 1700px)": {
					fontSize: "1.2rem",
				},
				"&::before": {
					border: "none",
				},
			},
		},
		MuiFilledInput: {
			root: {
				backgroundColor: primaryLight,
				color: textBlack,
				"&:hover": {
					backgroundColor: "none",
				},
			},
		},
		MuiButton: {
			outlinedSecondary: {
				borderColor: "#c6c8ca",
			},
		},
		MuiTab: {
			root: {
				border: "1px solid rgb(0 157 157 / 20%)",
				"&:hover": {
					backgroundColor: "rgb(0 225 235 / 20%)",
				},
				"&.Mui-selected": {
					transition: "opacity .1s ease",
					fontFamily: "DINBLD",
					color: "#fff",
					background: primary,
					borderRadius: "4px",
					border: 0,
				},
			},
			textColorInherit: {
				opacity: 0.6,
			},
		},
		MuiTabs: {
			indicator: {
				display: "flex",
				justifyContent: "center",
				backgroundColor: "transparent",
				bottom: "5px",
				"& > span": {
					maxWidth: 50,
					width: "100%",
				},
			},
		},
		MuiTableRow: {
			root: {
				"&.Mui-selected, Mui-selected:hover": {
					backgroundColor: "rgb(218 223 230) !important",
				},
			},
		},
		MuiCheckbox: {
			root: {
				color: "inherit",
				"&.Mui-checked ": {
					color: "#007eff",
				},
			},
		},
		MuiChip: {
			root: {
				backgroundColor: "#fff",
				border: "1px solid #ddd",
				fontSize: "14px",
				fontWeight: "bold",
				fontFamily: "Helvetica-Bold, sans-serif",
			},
		},
	},
	props: {
		MuiIconButton: {
			disableRipple: true,
		},
	},
});

export default theme;
