import React, {useState} from "react";
import {Box, Grid, makeStyles, Typography} from "@material-ui/core";
import {MasButton} from "../components/ui/MasButton";
import {MasInput} from "../components/ui/MasInput";
import {useDispatch} from "react-redux";
import {NewLogin} from "../redux/actions/login/loginAction";

export interface LoginState {
	username?: string;
	password?: string;
}
export default function LoginPage() {
	const classes = useStyles();
	const [state, setState] = useState<LoginState>({});
	const dispatch = useDispatch();

	const handleLogin = (evt: any) => {
		evt.preventDefault();
		if (!state?.username || !state?.password) {
			alert("     ");
			return;
		}
		dispatch(NewLogin({username: state.username, password: state.password}));
	};
	return (
		<>
			<div className={classes.wrapper}>
				<Box borderRadius={18} className={classes.form}>
					<Typography className={classes.headline} align="center" variant="h5">
						 
					</Typography>
					<form className={classes.content} noValidate autoComplete="on">
						<Grid container spacing={2} justify="center">
							<Grid item xs={10}>
								<MasInput
									fullWidth
									labelWidth={110}
									onChange={(evt) => {
										setState({...state, username: evt.target.value});
									}}
									value={state.username || ""}
									label=" "
									type="text"
								/>
							</Grid>
							<Grid item xs={10}>
								<MasInput
									labelWidth={88}
									fullWidth
									onChange={(evt) => {
										setState({...state, password: evt.target.value});
									}}
									value={state.password || ""}
									label="  "
									type="password"
								/>
							</Grid>
							<MasButton
								styleBtn={classes.btn}
								label=""
								type="submit"
								variant="contained"
								disableElevation
								color="secondary"
								onClick={handleLogin}
							/>
						</Grid>
					</form>
					<Box p={1} className={classes.footer}>
						<Typography align="center" variant="body2">
							powered by masr for advanced systems
						</Typography>
					</Box>
				</Box>
			</div>
		</>
	);
}

const useStyles = makeStyles((theme) => ({
	wrapper: {
		position: "relative",
		display: "flex",
		justifyContent: "center",
		alignItems: "center",
		width: "100vw",
		height: "100vh",
		overflow: "hidden",
	},
	form: {
		width: "30%",
		height: "450px",
		background: theme.palette.common.white,
		boxShadow: "0 0 20px rgb(0 0 0 / 15%)",
		padding: theme.spacing(2),
		position: "relative",
		overflow: "hidden",
		"&::after": {
			content: "''",
			position: "absolute",
			top: "80px",
			left: "50%",
			width: "250%",
			height: 250,
			backgroundColor: theme.palette.common.white,
			transform: "translate(-50%, 0)",
			borderRadius: "50%",
			zIndex: 1,
		},
	},
	headline: {
		fontFamily: "Dorid-Bld",
		height: "90px",
		background: `linear-gradient(45deg, #1b70b9 , #1bafb9 ) `,
		margin: theme.spacing(-2),
		zIndex: 5,
		display: "flex",
		justifyContent: "center",
		alignItems: "center",
		color: "#fff",
	},
	content: {
		position: "relative",
		top: "60px",
		left: "0",
		zIndex: 10,
	},
	btn: {
		margin: 0,
		marginTop: theme.spacing(2),
		padding: theme.spacing(1.2, 5),
		textAlign: "center",
		fontSize: "1.1rem",
	},
	footer: {
		position: "absolute",
		left: "50%",
		width: "100%",
		bottom: 10,
		transform: "translate(-50%, 0)",
		borderTop: `1px solid ${theme.palette.action.selected}`,
		color: theme.palette.grey[500],
		textTransform: "capitalize",
		textAlign: "center",
		paddingTop: "10px",
	},
	input: {
		"& .MuiOutlinedInput-root .MuiOutlinedInput-input": {
			padding: "21px 14px",
		},
	},
}));

