import React from "react";
import Select, {SelectProps} from "@material-ui/core/Select";
import InputLabel from "@material-ui/core/InputLabel";
import FormControl from "@material-ui/core/FormControl";
import {createStyles, makeStyles, Theme, withStyles} from "@material-ui/core";
import clsx from "clsx";
// import NativeSelect from "@material-ui/core/NativeSelect";
import InputBase from "@material-ui/core/InputBase";

const BootstrapInput = withStyles((theme: Theme) =>
	createStyles({
		root: {
			"label + &": {
				marginTop: theme.spacing(3),
			},
		},
		input: {
			borderRadius: 4,
			position: "relative",
			backgroundColor: theme.palette.background.paper,
			border: "1px solid #ced4da",
			fontSize: "1rem",
			padding: "10px 26px 10px 12px",
			transition: theme.transitions.create(["border-color", "box-shadow"]),
			height: "auto",
			"&:focus": {
				borderRadius: 4,
				borderColor: "#80bdff",
				boxShadow: "0 0 0 0.2rem rgba(0,123,255,.25)",
			},
		},
	})
)(InputBase);

const useStyles = makeStyles(() => ({
	formControl: {},
	lable: {},
	menu: {
		minWidth: "300px",
	},
	menuItem: {},
}));

export interface MasSelectProps extends SelectProps {
	items: any[];
	itemText: string | null;
	itemValue: string | null;
	label?: string;
	labelStyle?: string;
	disabled?: any;
	defaultValue?: any;
	onChange?: (e: any) => void;
	styleItem?: string;
	styleMenue?: string;
	styleFormControl?: string;
	name?: string;
	multiple?: boolean;
	fullWidth?: boolean;
}
export function MasSelect(ownProp: MasSelectProps) {
	const classes = useStyles();
	const {
		items,
		onChange,
		multiple,
		styleItem,
		styleMenue,
		styleFormControl,
		inputRef,
		disabled,
		name,
		defaultValue,
		labelStyle,
		label,
		itemValue,
		itemText,
		fullWidth,
		...restProps
	} = ownProp;
	const MasSelectitems: any[] = items;

	return (
		<FormControl
			fullWidth={fullWidth}
			variant="filled"
			className={clsx(styleFormControl, classes.formControl)}
		>
			{label && (
				<InputLabel className={clsx(labelStyle, classes.lable)} htmlFor={name}>
					{label}
				</InputLabel>
			)}
			<Select
				native
				name={name}
				defaultValue={defaultValue}
				disabled={disabled}
				className={clsx(styleMenue, classes.menu)}
				multiple={multiple}
				input={<BootstrapInput />}
				onChange={(e) => (onChange ? onChange(e) : null)}
				{...restProps}
			>
				{MasSelectitems ? (
					MasSelectitems.map((item: any, index: number) => {
						return (
							<option
								key={index}
								value={itemValue ? (item[itemValue] ? item[itemValue] : "") : index}
							>
								{itemText ? item[itemText] : item}
							</option>
						);
					})
				) : (
					<option className={classes.menuItem} key={1} value={0} />
				)}
			</Select>
		</FormControl>
	);
}
