import * as React from "react";
import {Theme, withStyles, createStyles} from "@material-ui/core/styles";
import {
	InputProps,
	MenuItem,
	Paper,
	Select,
	Snackbar,
	Table,
	TableBody,
	TableCell,
	TableContainer,
	TableHead,
	TableRow,
	FormControl,
	OutlinedInput,
	fade,
	IconButton,
	Typography,
	Box,
	Fab,
} from "@material-ui/core";
import SettingsApplicationsRoundedIcon from "@material-ui/icons/SettingsApplicationsRounded";
import DateFnsUtils from "@date-io/date-fns";
import {MuiPickersUtilsProvider, KeyboardDatePicker} from "@material-ui/pickers";
import MuiAlert from "@material-ui/lab/Alert";
import CommentIcon from "@material-ui/icons/Comment";

import {
	Bill,
	CONNECTION_STATUS_TYPE,
	Ctg,
	LookUp,
	READING_TYPE,
	READING_TYPEMap,
	SubConnection,
} from "../../proto/service_pb";
import {timeStampToDate} from "../../client/converter";
import {SubConnectionsEditor} from "../../components/sub-connections";
import {MasTextField} from "../../components/ui/MasTextField";
import {MasButton} from "../../components/ui/MasButton";
import MasSelectBox from "../../components/ui/mas-selectbox";
import MasTooltip from "../../components/ui/MasTooltip";

const LuReadType = {
	[READING_TYPE.ACTUAL]: "",
	[READING_TYPE.AVERAGE]: "",
	[READING_TYPE.ESTIMATE]: "",
};

const headers = [
	"",
	" ",
	"",
	" ",
	" ",
	" ",
	" ",
	"",
	" ",
	" ",
	" ",
	"",
	"",
	" ",
];

const LuConnectionStatus = {
	[CONNECTION_STATUS_TYPE.CONNECTED_WITHOUT_METER]: "",
	[CONNECTION_STATUS_TYPE.CONNECTED_WITH_METER]: " ",
	[CONNECTION_STATUS_TYPE.DISCONNECTED_WITH_METER]: " ",
	[CONNECTION_STATUS_TYPE.DISCONNECTED_WITHOUT_METER]: "  ",
};

export interface UserData {
	//ConnStatsus?: CONNECTION_STATUS_TYPEMap[keyof CONNECTION_STATUS_TYPEMap];
	//MeterOpState?: MeterOperationStatusMap[keyof MeterOperationStatusMap];
	isConnected?: boolean;
	haveMeter?: boolean;
	CrReading?: number;
	PrReading?: number;
	ReadType?: READING_TYPEMap[keyof READING_TYPEMap];
	CrDate?: Date;
	PrDate?: Date;
	Consumption?: number;
	MeterDiameter?: number;
	MainNoUnits?: number;
	MainCtype?: string;
	CtypeGroupId?: string;
	DbtAmt?: number;
	CrdAmt?: number;
	InstalsAmt?: number;
	SubConnections?: Array<SubConnection.AsObject>;
	IsSewerConnection?: boolean;
}

function Alert(props) {
	return <MuiAlert elevation={6} variant="filled" {...props} />;
}

export interface Notification {
	sevirity?: string;
	open?: boolean;
	msg?: string;
}

export interface MyInputProps extends InputProps {
	itm: string;
	type?: string | "text";
}

interface DbtCrdt {
	DbtAmt: number;
	CrdAmt: number;
	InstalsAmt: number;
}
export interface RecalcItemProps {
	billIndex: number;
	oldBill: Bill.AsObject;
	newBill: Bill.AsObject;
	ctgs: Array<Ctg.AsObject>;
	meterDiameters: Array<LookUp.AsObject>;
	isCancelledRequest: boolean;
	formNo?: number;
	onChargeClick?: (billIndex: number, userData: UserData) => void;
	onPreviewClick?: (billIndex: number, userData: UserData) => void;
	onUserDataChange?: (billIndex: number, userData: UserData) => void;
	onApplyToAll?: (billIndex: number, userData: UserData) => void;
	classes?: any;
	initalUserData?: UserData;
	showCustKey?: boolean;
}

export interface RecalcItemState {
	open?: Notification;
	userData?: UserData;
	showPreview?: boolean;
	showOldSub?: boolean;
	showNewSub?: boolean;
	calculated?: boolean;
	disExcute?: boolean;
	oldDbtCrd?: DbtCrdt;
	CrdAmt?: number;
	DbtAmt?: number;
	InstalsAmt?: number;
}
class RecalcItemWithStyles extends React.Component<RecalcItemProps, RecalcItemState> {
	constructor(props: RecalcItemProps) {
		super(props);
		this.state = {userData: {...props.initalUserData}, open: {}};
		this.setMyState = this.setMyState.bind(this);
		this.setUserData = this.setUserData.bind(this);
		this.handelShowBill = this.handelShowBill.bind(this);
		this.handleChargeRquest = this.handleChargeRquest.bind(this);
		this.handleClose = this.handleClose.bind(this);
		this.handleAplyToAll = this.handleAplyToAll.bind(this);
	}
	setMyState(s: Partial<RecalcItemState>, fireOnChange: boolean = true) {
		this.setState({...this.state, ...s}, () => {
			if (fireOnChange) {
				if (this.props.onUserDataChange) {
					this.props.onUserDataChange(this.props.billIndex, this.state.userData);
				}
			}
		});
	}
	setUserData(u: Partial<UserData>, fireOnChange: boolean = true) {
		this.setMyState({userData: {...this.state.userData, ...u}});
	}
	handelShowBill() {
		if (this.props.onPreviewClick) {
			this.props.onPreviewClick(this.props.billIndex, this.state.userData);
		}
	}
	handleChargeRquest() {
		if (this.props.onChargeClick) {
			this.props.onChargeClick(this.props.billIndex, this.state.userData);
		}
	}
	handleClose() {}
	handelConfirm() {
		return Promise.reject(" ");
	}
	handleAplyToAll() {
		if (this.props.onApplyToAll) {
			this.props.onApplyToAll(this.props.billIndex, this.state.userData);
		}
	}
	componentWillReceiveProps(nwProps: RecalcItemProps) {
		if (nwProps.initalUserData !== this.state.userData) {
			// console.log(`setting user data ${this.props.billIndex}`);
			this.setUserData(nwProps.initalUserData, false);
		}
	}
	componentDidMount() {
		let d = this.props.oldBill;
		let oldBill = d;
		const dbtAmt = (d.ftransactionsList || [])
			.filter((a) => a.amount > 0 && (a.code === "DBT_AMT" || a.code === "CRDT_AMT"))
			.reduce((a, b) => {
				return a + (b.amount || 0);
			}, 0);
		const crdAmt = (d.ftransactionsList || [])
			.filter((a) => a.amount < 0 && (a.code === "DBT_AMT" || a.code === "CRDT_AMT"))
			.reduce((a, b) => {
				return a + (b.amount || 0);
			}, 0);
		const instalsAmt = (d.ftransactionsList || [])
			.filter((a) => a.amount > 0 && a.code === "INSTALLS_AMT")
			.reduce((a, b) => {
				return a + (b.amount || 0);
			}, 0);
		let oldConnStatus =
			d.customer?.property.servicesList[0]?.connection?.connectionstatus;
		let isConnected: boolean =
			oldConnStatus === CONNECTION_STATUS_TYPE.CONNECTED_WITHOUT_METER ||
			oldConnStatus === CONNECTION_STATUS_TYPE.CONNECTED_WITH_METER;
		let haveMeter: boolean =
			oldConnStatus === CONNECTION_STATUS_TYPE.CONNECTED_WITH_METER ||
			oldConnStatus === CONNECTION_STATUS_TYPE.DISCONNECTED_WITH_METER;
		let nwUserData: UserData = {
			...this.state.userData,
			haveMeter: haveMeter,
			isConnected: isConnected,
			ReadType: oldBill?.servicesreadingsList[0]?.reading?.readtype,
			//ConnStatsus:        olB?.customer?.property?.servicesList[0]?.connection?.connectionstatus,
			//MeterOpState:        olB?.customer?.property?.servicesList[0]?.connection?.meter?.opstatus,
			MainNoUnits: oldBill?.customer?.property?.servicesList[0]?.connection?.nounits,
			MainCtype: oldBill?.customer?.property?.servicesList[0]?.connection?.ctype?.ctype,
			CtypeGroupId:
				oldBill?.customer?.property?.servicesList[0]?.connection?.ctype?.ctypegroupid,
			CrReading: d?.servicesreadingsList[0]?.reading?.crreading,
			PrReading: d?.servicesreadingsList[0]?.reading?.prreading,
			Consumption: d?.servicesreadingsList[0]?.reading?.consump,
			CrDate: timeStampToDate(oldBill?.servicesreadingsList[0]?.reading?.crdate),
			PrDate: timeStampToDate(oldBill?.servicesreadingsList[0]?.reading?.prdate),
			MeterDiameter:
				oldBill?.customer?.property?.servicesList[0]?.connection?.meter?.diameter,
			SubConnections:
				oldBill?.customer?.property?.servicesList[0]?.connection?.subconnectionsList,
			DbtAmt: dbtAmt,
			InstalsAmt: instalsAmt,
			CrdAmt: crdAmt,
			IsSewerConnection:
				oldBill?.customer?.property?.servicesList?.length > 1 ? true : false,
		};
		this.setMyState({
			userData: nwUserData,
			CrdAmt: crdAmt,
			DbtAmt: dbtAmt,
			InstalsAmt: instalsAmt,
		});
	}
	shouldComponentUpdate(nextProps: RecalcItemProps, nextState: RecalcItemState) {
		const stat = JSON.stringify(this.state);
		const nextstat = JSON.stringify(nextState);
		if (nextProps.newBill !== this.props.newBill) {
			return true;
		}
		if (stat === nextstat) {
			return false;
		} else {
			return true;
		}
	}
	render() {
		const {open, userData, showOldSub, showNewSub, oldDbtCrd} = this.state;
		const {newBill, oldBill, meterDiameters, ctgs, classes, showCustKey} = this.props;
		const o = oldBill;
		const setUserData = this.setUserData;
		const setMyState = this.setMyState;
		console.log("render");
		console.log(this.state.userData, "user data", oldBill);
		return (
			<MuiPickersUtilsProvider utils={DateFnsUtils}>
				<div className={classes.root}>
					<Paper style={{maxWidth: "100%"}}>
						<Paper elevation={0} className={classes.billHeader}>
							<div>
								<Typography variant="subtitle1" component="span">
									 
									<span className={classes.billingDate}>
										{timeStampToDate(o?.bilngdate)?.toLocaleDateString()}
									</span>
								</Typography>
							</div>
							<div>
								<Typography variant="subtitle1" component="span">
									 
									<span className={classes.billingDate}>{o?.clbalance.toFixed(2)}</span>
								</Typography>
							</div>
							<div>
								<Typography variant="subtitle1" component="span">
									 
									<span className={classes.billingDate}>{o?.paymentno}</span>
								</Typography>
							</div>
							{showCustKey && (
								<div>
									<Typography variant="subtitle1" component="span">
										 
										<span className={classes.billingDate}>{o?.customer.custkey}</span>
									</Typography>
								</div>
							)}
						</Paper>
						<TableContainer className={classes.table}>
							<Table aria-label="simple table">
								<TableHead>
									<StyledTableRow>
										{headers.map((header: any, index: number) => (
											<StyledTableCell key={index}>{header}</StyledTableCell>
										))}
									</StyledTableRow>
								</TableHead>
								<TableBody>
									<StyledTableRow className={classes.oldBill}>
										<StyledTableCell padding="checkbox" className={classes.headCell}>
											<span className={classes.headline}></span>
										</StyledTableCell>
										<StyledTableCell>
											{
												LuConnectionStatus[
													o?.customer?.property?.servicesList[0]?.connection
														?.connectionstatus
												]
											}
										</StyledTableCell>
										<StyledTableCell>
											{o?.customer?.property?.servicesList[0]?.connection?.meter
												? ""
												: ""}
										</StyledTableCell>
										<StyledTableCell>
											{
												o?.customer?.property?.servicesList[0]?.connection?.meter
													?.diameter
											}
										</StyledTableCell>
										<StyledTableCell>
											{LuReadType[o?.servicesreadingsList[0]?.reading?.readtype]}
										</StyledTableCell>
										<StyledTableCell>
											{o?.servicesreadingsList[0]?.reading?.prreading}
										</StyledTableCell>
										<StyledTableCell>
											{o?.servicesreadingsList[0]?.reading?.crreading}
										</StyledTableCell>
										<StyledTableCell>
											{o?.servicesreadingsList[0]?.reading?.consump}
										</StyledTableCell>
										<StyledTableCell>
											{o?.customer?.property?.servicesList[0]?.connection?.nounits}
										</StyledTableCell>
										<StyledTableCell>{oldDbtCrd?.DbtAmt}</StyledTableCell>
										<StyledTableCell>{oldDbtCrd?.CrdAmt}</StyledTableCell>
										<StyledTableCell>{oldDbtCrd?.InstalsAmt}</StyledTableCell>
										<StyledTableCell>
											<MasTooltip title=" ">
												<IconButton
													size="small"
													style={{
														boxShadow: "none",
														margin: "0 4px",
													}}
													onClick={() => {
														//setShowOldSub(true);
														this.setMyState({showOldSub: true});
													}}
												>
													<SettingsApplicationsRoundedIcon
														style={{color: "#7f8990", fontSize: "28px"}}
													></SettingsApplicationsRoundedIcon>
												</IconButton>
											</MasTooltip>
											<SubConnectionsEditor
												readonly={true}
												ctgs={ctgs}
												onCancel={() => {
													this.setMyState({showOldSub: false});
												}}
												open={showOldSub}
												subConnections={
													o?.customer?.property?.servicesList[0]?.connection
														?.subconnectionsList
												}
											/>
											{o?.customer?.property?.servicesList[0]?.connection
												?.subconnectionsList?.length > 1
												? " "
												: ""}
											{o?.customer?.property?.servicesList[0]?.connection?.ctype
												?.description ||
												o?.customer?.property?.servicesList[0]?.connection?.ctype?.ctype}
										</StyledTableCell>
										<StyledTableCell>
											{o?.customer?.property?.servicesList?.length > 1 ? "" : ""}
										</StyledTableCell>
									</StyledTableRow>
									<StyledTableRow className={classes.newBill}>
										<StyledTableCell padding="checkbox" className={classes.headCell2}>
											<span className={classes.headline}></span>
										</StyledTableCell>
										<StyledTableCell>
											<FormControl variant="outlined" className={classes.formControl}>
												<Select
													labelWidth={0}
													labelId="demo-simple-select-label1"
													id="demo-simple-select-1"
													input={<SelectInput />}
													MenuProps={MenuProps}
													value={userData?.isConnected ? "1" : "0"}
													onChange={(evt) => {
														let isConnected =
															Number(evt.target.value || 0) === 1 ? true : false;
														this.setUserData({isConnected: isConnected});
													}}
												>
													<StyledMenuItem key={0} value="0">
														 
													</StyledMenuItem>
													<StyledMenuItem key={1} value="1">
														
													</StyledMenuItem>
												</Select>
											</FormControl>
										</StyledTableCell>
										<StyledTableCell>
											<FormControl variant="outlined" className={classes.formControl}>
												<Select
													labelWidth={0}
													labelId="demo-simple-select-label0"
													id="demo-simple-select-2"
													input={<SelectInput />}
													MenuProps={MenuProps}
													value={userData?.haveMeter ? "1" : "0"}
													onChange={(event) => {
														let hvM = Number(event.target.value) === 1 ? true : false;
														this.setUserData({
															haveMeter: hvM,
															MeterDiameter: hvM ? userData?.MeterDiameter : null,
															ReadType: hvM ? userData?.ReadType : null,
														});
													}}
												>
													<StyledMenuItem key={0} value="0">
														 
													</StyledMenuItem>
													<StyledMenuItem key={1} value="1">
														
													</StyledMenuItem>
												</Select>
											</FormControl>
										</StyledTableCell>
										<StyledTableCell>
											<FormControl variant="outlined" className={classes.formControl}>
												<Select
													disabled={!userData?.haveMeter}
													labelId="demo-simple-select-label2"
													input={<SelectInput />}
													MenuProps={MenuProps}
													id="demo-simple-select-3"
													value={userData?.MeterDiameter || ""}
													onChange={(event) => {
														this.setUserData({
															MeterDiameter: event.target.value as any,
														});
													}}
												>
													{meterDiameters.map((key, itr) => {
														return (
															<MenuItem key={itr} value={key.code}>
																{key.description}
															</MenuItem>
														);
													})}
												</Select>
											</FormControl>
										</StyledTableCell>
										<StyledTableCell>
											<FormControl variant="outlined" className={classes.formControl}>
												<Select
													disabled={!userData?.haveMeter}
													input={<SelectInput />}
													MenuProps={MenuProps}
													labelId="demo-simple-select-label5"
													id="demo-simple-select-4"
													value={userData?.ReadType?.toString() || ""}
													onChange={(event) => {
														this.setUserData({
															ReadType: Number(event.target.value) as any,
														});
													}}
												>
													{Object.keys(LuReadType).map((key, itr) => {
														return (
															<MenuItem key={itr} value={key === "" ? "" : Number(key)}>
																{LuReadType[key]}
															</MenuItem>
														);
													})}
												</Select>
											</FormControl>
										</StyledTableCell>
										<StyledTableCell className={classes.reading}>
											<MasTextField
												disabled={userData?.ReadType !== READING_TYPE.ACTUAL}
												label=""
												styleTextFiled={classes.cellInput}
												required={false}
												value={userData?.PrReading?.toString() || ""}
												type="number"
												onChange={(evt) => {
													let val = Number(evt.target.value);
													let consump = userData.CrReading - val;
													if (consump < 0) {
														consump = 0;
													}
													this.setUserData({PrReading: val, Consumption: consump});
												}}
											/>
											<KeyboardDatePicker
												disableFuture
												autoOk
												openTo="year"
												views={["year", "month", "date"]}
												fullWidth
												variant="inline"
												format="yyyy/MM/dd"
												inputVariant="outlined"
												label=""
												placeholder=" "
												value={userData?.PrDate}
												className={classes.date}
												onChange={(date) => {
													this.setUserData({PrDate: date});
												}}
												KeyboardButtonProps={{
													"aria-label": "change date",
												}}
											/>
										</StyledTableCell>
										<StyledTableCell className={classes.reading}>
											<MasTextField
												styleTextFiled={classes.cellInput}
												disabled={userData?.ReadType !== READING_TYPE.ACTUAL}
												label=""
												required={false}
												value={userData?.CrReading?.toString() || ""}
												type="number"
												onChange={(evt) => {
													let val = Number(evt.target.value || 0);
													let consump = (val || 0) - (userData.PrReading || 0);
													if (consump < 0) {
														consump = 0;
													}
													this.setUserData({CrReading: val, Consumption: consump});
												}}
											/>
											<KeyboardDatePicker
												disableFuture
												autoOk
												openTo="year"
												views={["year", "month", "date"]}
												fullWidth
												variant="inline"
												className={classes.date}
												format="yyyy/MM/dd"
												inputVariant="outlined"
												label=""
												placeholder=" "
												value={userData?.CrDate}
												onChange={(date) => {
													this.setUserData({CrDate: date});
												}}
												KeyboardButtonProps={{
													"aria-label": "change date",
												}}
											/>
										</StyledTableCell>
										<StyledTableCell>
											<MasTextField
												disabled={userData?.ReadType === READING_TYPE.ACTUAL}
												label=""
												styleTextFiled={classes.cellInput}
												required={false}
												value={userData?.Consumption || 0}
												type="number"
												onChange={(evt) => {
													let val = Number(evt.target.value || 0);
													this.setUserData({Consumption: val});
												}}
											/>
										</StyledTableCell>
										<StyledTableCell>
											<MasTextField
												label=""
												styleTextFiled={classes.cellInput}
												required={false}
												value={userData?.MainNoUnits?.toString() || ""}
												onChange={(evt) => {
													let val = Number(evt.target.value || 0);
													this.setUserData({MainNoUnits: val});
												}}
											/>
										</StyledTableCell>
										<StyledTableCell>
											<MasTextField
												label=""
												styleTextFiled={classes.cellInput}
												required={false}
												value={userData?.DbtAmt || 0}
												onChange={(evt) => {
													let val = Number(evt.target.value || 0);
													this.setUserData({DbtAmt: val});
												}}
											/>
										</StyledTableCell>
										<StyledTableCell>
											<MasTextField
												label=""
												styleTextFiled={classes.cellInput}
												required={false}
												type="number"
												value={-userData?.CrdAmt || 0}
												onChange={(evt) => {
													let val = Number(evt.target.value || 0);
													this.setUserData({CrdAmt: -val});
												}}
											/>
										</StyledTableCell>
										<StyledTableCell>
											<MasTextField
												label=""
												styleTextFiled={classes.cellInput}
												required={false}
												type="number"
												value={userData?.InstalsAmt || 0}
												onChange={(evt) => {
													let val = Number(evt.target.value || 0);
													setUserData({InstalsAmt: val});
												}}
											/>
										</StyledTableCell>
										<StyledTableCell>
											{newBill?.customer?.property?.servicesList[0]?.connection
												?.subconnectionsList?.length > 1 ? (
												<Typography variant="subtitle2" className={classes.textMokhdalt}>
													
												</Typography>
											) : (
												""
											)}

											<div
												style={{
													display: "flex",
													flexDirection: "row",
													alignItems: "center",
													justifyContent: "space-between",
												}}
											>
												<MasTooltip title=" ">
													<IconButton
														size="small"
														style={{
															boxShadow: "none",
															margin: "0 4px",
														}}
														onClick={() => {
															setMyState({showNewSub: true});
														}}
													>
														<SettingsApplicationsRoundedIcon
															style={{color: "#7f8990", fontSize: "28px"}}
														></SettingsApplicationsRoundedIcon>
													</IconButton>
												</MasTooltip>
												<FormControl variant="outlined" className={classes.formControl}>
													<MasSelectBox
														textFieldProps={{
															required: false,
															label: "",
															variant: "outlined",
														}}
														displayMemper="description"
														options={ctgs}
														value={userData?.MainCtype || ""}
														valueMember="ctype"
														onChange={(evt, x: Ctg.AsObject) => {
															if (x !== null) {
																setUserData({
																	MainCtype: x.ctype,
																	CtypeGroupId: x.ctypegroupid,
																});
															}
														}}
													/>
													{/* {userData?.MainCtype} */}
												</FormControl>
												{showNewSub && (
													<SubConnectionsEditor
														readonly={false}
														ctgs={ctgs}
														onCancel={() => {
															setMyState({showNewSub: false});
														}}
														onOk={(cns) => {
															setMyState({
																showNewSub: false,
																userData: {...userData, SubConnections: cns},
															});
														}}
														open={showNewSub}
														subConnections={userData?.SubConnections}
													/>
												)}
											</div>
										</StyledTableCell>
										<StyledTableCell>
											<FormControl variant="outlined" className={classes.formControl}>
												<Select
													labelWidth={0}
													labelId="demo-simple-select-label6"
													id="demo-simple-select-6"
													input={<SelectInput />}
													MenuProps={MenuProps}
													value={userData?.IsSewerConnection ? "1" : "0"}
													onChange={(evt) => {
														let isSewer =
															Number(evt.target.value || 0) === 1 ? true : false;
														this.setUserData({IsSewerConnection: isSewer});
													}}
												>
													<StyledMenuItem key={0} value="0">
														  
													</StyledMenuItem>
													<StyledMenuItem key={1} value="1">
														  
													</StyledMenuItem>
												</Select>
											</FormControl>
										</StyledTableCell>
									</StyledTableRow>
								</TableBody>
							</Table>
						</TableContainer>
						<Paper className={classes.btns}>
							{!this.props.isCancelledRequest && (
								<>
									<MasButton
										variant="outlined"
										color="secondary"
										onClick={this.handelShowBill}
										label=" "
										type="button"
									></MasButton>
									<MasButton
										variant="contained"
										disableElevation
										color="primary"
										onClick={this.handleChargeRquest}
										label=" "
										type="button"
									></MasButton>
								</>
							)}

							{this.props.billIndex === 0 && (
								<MasButton
									variant="contained"
									disableElevation
									color="default"
									onClick={this.handleAplyToAll}
									label="    "
									type="button"
								></MasButton>
							)}
							<Box display="inline-block" position="relative">
								<Fab
									className={classes.billComment}
									aria-label="comment"
									color="default"
									size="small"
								>
									<CommentIcon
										style={{
											color: "#8b939a",
										}}
									/>
								</Fab>
								<div className={classes.billCommentContent}>
									{o?.comment === undefined || ""
										? "    "
										: o?.comment}
								</div>
							</Box>
						</Paper>
					</Paper>
					<Snackbar open={open?.open} autoHideDuration={2000} onClose={this.handleClose}>
						<Alert onClose={this.handleClose} severity={open?.sevirity}>
							{open?.msg}
						</Alert>
					</Snackbar>
				</div>
			</MuiPickersUtilsProvider>
		);
	}
}

