import React, {Component} from "react";
import Autocomplete, {AutocompleteProps} from "@material-ui/lab/Autocomplete";
import {MasTextField} from "./MasTextField";
import {createStyles, TextFieldProps, Theme, withStyles} from "@material-ui/core";

interface ISelectBox extends Partial<AutocompleteProps<any, any, any, any>> {
	displayMemper?: string;
	valueMember?: string;
	textFieldProps: TextFieldProps;
	classes: any;
}

const styles = (theme: Theme) =>
	createStyles({
		paper: {
			boxShadow: "none",
			margin: 0,
			color: "#586069",
			fontSize: "1rem",
			position: "relative",
		},
		option: {
			minHeight: "auto",
			alignItems: "flex-start",
			padding: 8,
			'&[aria-selected="true"]': {
				backgroundColor: "transparent",
			},
			'&[data-focus="true"]': {
				backgroundColor: theme.palette.action.hover,
			},
		},
		popperDisablePortal: {
			position: "relative",
		},
		popper: {
			position: "absolute",
			minHeight: "150px",
		},
		iconSelected: {
			width: 15,
			height: 15,
			marginRight: 5,
			marginLeft: 3,
			color: "#fff",
			backgroundColor: theme.palette.primary.main,
		},
		color: {
			width: 14,
			height: 14,
			flexShrink: 0,
			borderRadius: 3,
			marginRight: 8,
			marginTop: 2,
		},
		text: {
			flexGrow: 1,
			color: "#282b2e",
			fontSize: "1rem",
		},
		close: {
			opacity: 0.6,
			width: 18,
			height: 18,
		},
		input: {
			marginBottom: 0,
		},
	});

class MasSelectBox extends Component<ISelectBox> {
	constructor(props: ISelectBox) {
		super(props);
		this.state = this.props;
	}
	shouldComponentUpdate(nextProps: any, nextState: any) {
		if (nextProps.value === this.props.value) {
			return false;
		} else {
			return true;
		}
	}

	render() {
		console.log("render masSelect copy");
		const {displayMemper, classes, textFieldProps, valueMember, options, ...rest} =
			this.props;

		return (
			<Autocomplete
				options={options || []}
				classes={{
					paper: classes.paper,
					option: classes.option,
					popper: classes.popper,
					popperDisablePortal: classes.popperDisablePortal,
				}}
				selectOnFocus={false}
				style={{width: 260}}
				getOptionLabel={(val: any) => {
					let lbl = val[displayMemper as string];
					if (lbl) {
						return lbl;
					}
					if (valueMember) {
						let tar = (options || []).filter((a: any) => {
							return a[valueMember] === val;
						})[0];
						lbl = tar?.[displayMemper as string];
					}
					return lbl || "";
				}}
				renderInput={(params) => <MasTextField {...params} {...textFieldProps} />}
				{...rest}
			/>
		);
	}
}

export default withStyles(styles, {withTheme: true})(MasSelectBox);
