import React, {useEffect} from "react";
import {
	Dialog,
	DialogTitle,
	DialogContent,
	Paper,
	Table,
	TableHead,
	TableRow,
	TableCell,
	TableBody,
	Button,
	makeStyles,
	DialogActions,
	Grid,
	Typography,
	withStyles,
	FormControl,
	IconButton,
	FormControlLabel,
	Switch,
	fade,
} from "@material-ui/core";
import {Ctg, SubConnection} from "../proto/service_pb";
import {AddCircleOutlineRounded, DeleteForeverRounded} from "@material-ui/icons";
import {MasTextField} from "./ui/MasTextField";
import MasTooltip from "./ui/MasTooltip";
import {MasButton} from "./ui/MasButton";
import MasSelectBox from "./ui/mas-selectbox";
import {red} from "@material-ui/core/colors";

export interface SubConnectionsEditorProps {
	ctgs?: Array<Ctg.AsObject>;
	subConnections?: Array<SubConnection.AsObject>;
	open?: boolean;
	readonly?: boolean;
	onCancel?: () => void;
	onOk?: (c: Array<SubConnection.AsObject>) => void;
}

const useStyles = makeStyles((theme) => ({
	formControl: {
		width: "100%",
		minWidth: "160px",
	},
	switchLabel: {
		color: theme.palette.grey[500],
		"&.MuiFormControlLabel-label": {
			fontSize: "0.75rem",
		},
	},
	deleteCtype: {
		padding: 4,
		background: red[500],
		borderRadius: "50%",
		margin: 16,
		color: "#fff",
		"&:hover": {
			background: fade(red[500], 0.8),
		},
	},
}));

const StyledTableCell = withStyles(() => ({
	head: {
		fontFamily: "DINBLD, Arial , sans-serif",
		textAlign: "center",
		padding: "8px",
	},
	body: {
		fontSize: "0.9rem",
		border: "1px solid #d4d8dc",
		textAlign: "center",
		"&:last-child": {
			borderRightColor: "transparent",
		},
	},
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
	root: {
		"&:nth-of-type(even)": {
			backgroundColor: theme.palette.action.hover,
		},
	},
}))(TableRow);

export const SubConnectionsEditor = (props: SubConnectionsEditorProps) => {
	const {open, onOk, onCancel, ctgs, readonly} = props;
	const classes = useStyles();
	const [conns, setConns] = React.useState<Array<SubConnection.AsObject>>(
		[
			...((props.subConnections || []).map((a) => {
				return {...a};
			}) || []),
		] || []
	);
	const [isAutoPer, setIsAutoPer] = React.useState<boolean>(true);
	useEffect(() => {
		if (!props.subConnections) {
			setConns([]);
		} else {
			let cp = props.subConnections.map((a) => {
				return {...a};
			});
			setConns([...cp]);
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, []);
	useEffect(() => {
		if (!props.subConnections) {
			setConns([]);
		} else {
			let cp = props.subConnections.map((a) => {
				return {...a};
			});
			setConns([...cp]);
		}
	}, [props.subConnections]);
	const handleAdd = () => {
		setConns([...(conns || []), {ctype: {tariffsList: []}}]);
	};
	const handleRemove = (i) => {
		let sli = conns.slice(0, i);
		let sli2 = conns.slice(i + 1);
		setConns([...sli, ...sli2]);
	};
	const handleOk = () => {
		if (onOk) {
			onOk(conns);
		}
	};

	const handleCancel = () => {
		setConns([...(props.subConnections || [])]);
		if (onCancel) {
			onCancel();
		}
	};
	const calcPer = () => {
		if (isAutoPer) {
			let totalWeight = 0;
			conns.forEach((c) => {
				totalWeight = totalWeight + (c.nounits || 1) * (c.ctype.weigth || 1);
			});
			conns.forEach((c) => {
				let wgt = (c.nounits || 1) * (c.ctype.weigth || 1);
				let p = (100 * wgt) / totalWeight;
				c.consumptionpercentage = p;
			});
			setConns([...conns]);
		} else {
			setConns([...conns]);
		}
	};
	const toggleChecked = () => {
		setIsAutoPer((prev) => !prev);
		console.log("is autoper", isAutoPer);
	};

	return (
		<Dialog
			open={open}
			scroll="paper"
			maxWidth="md"
			fullWidth
			aria-labelledby="scroll-dialog-title"
			aria-describedby="scroll-dialog-description"
			fullScreen={false}
		>
			<DialogTitle
				style={{padding: "8px", paddingRight: "22px"}}
				id="scroll-dialog-title"
			>
				<Grid container direction="row" justify="space-between" alignItems="center">
					<Grid item xs={4}>
						<Typography variant="h6"></Typography>
					</Grid>
					<Grid item xs={4} style={{textAlign: "center"}}>
						<MasTooltip title="   ">
							<Button
								variant="outlined"
								disabled={readonly}
								onClick={handleAdd}
								disableElevation
								color="primary"
								startIcon={<AddCircleOutlineRounded style={{fontSize: "28px"}} />}
							>
								
							</Button>
						</MasTooltip>
					</Grid>
					<Grid item xs={4} style={{textAlign: "left"}}>
						<MasTooltip title="     ">
							<FormControlLabel
								className={classes.switchLabel}
								control={<Switch checked={isAutoPer} onChange={toggleChecked} />}
								label="  "
								labelPlacement="end"
							/>
						</MasTooltip>
					</Grid>
				</Grid>
			</DialogTitle>
			<DialogContent dividers={true}>
				<Paper>
					<Table>
						<TableHead>
							<StyledTableRow>
								<StyledTableCell></StyledTableCell>
								<StyledTableCell> </StyledTableCell>
								<StyledTableCell> </StyledTableCell>
								<StyledTableCell>  </StyledTableCell>
								<StyledTableCell padding="checkbox"></StyledTableCell>
							</StyledTableRow>
						</TableHead>
						<TableBody>
							{(conns || []).map((m, i) => {
								return (
									<StyledTableRow key={i}>
										<StyledTableCell>
											<FormControl className={classes.formControl} variant="outlined">
												{/* {m?.ctype?.description} */}
												<MasSelectBox
													disabled={readonly}
													textFieldProps={{variant: "outlined"}}
													displayMemper="description"
													options={ctgs}
													id="demo-simple-select"
													value={m.ctype}
													onChange={(evt, x: Ctg.AsObject, y) => {
														m.ctype = x;
													}}
													onClose={() => calcPer()}
												/>
											</FormControl>
										</StyledTableCell>
										<StyledTableCell>
											<MasTextField
												variant="outlined"
												required={false}
												label=""
												disabled={readonly}
												type="number"
												value={m.nounits || ""}
												onChange={(event) => {
													m.nounits = event.target.value as any;
													//setConns([...conns]);
													calcPer();
												}}
											/>
										</StyledTableCell>
										<StyledTableCell>
											<MasTextField
												variant="outlined"
												required={false}
												label=""
												disabled={readonly}
												type="number"
												value={m.consumptionpercentage?.toFixed(2) || ""}
												onChange={(event) => {
													m.consumptionpercentage = Number(event.target.value || 0);
													setConns([...conns]);
												}}
											/>
										</StyledTableCell>
										<StyledTableCell>
											<MasTextField
												variant="outlined"
												required={false}
												label=""
												disabled={readonly}
												value={m.estimateconsumption || ""}
												onChange={(event) => {
													m.estimateconsumption = event.target.value as any;
													setConns([...conns]);
												}}
											/>
										</StyledTableCell>
										<StyledTableCell
											padding="checkbox"
											style={{border: "0.1px solid #d4d8dc"}}
										>
											<MasTooltip title="">
												<IconButton
													className={classes.deleteCtype}
													disabled={readonly}
													onClick={() => {
														handleRemove(i);
													}}
												>
													<DeleteForeverRounded color="inherit" />
												</IconButton>
											</MasTooltip>
										</StyledTableCell>
									</StyledTableRow>
								);
							})}
						</TableBody>
					</Table>
				</Paper>
			</DialogContent>
			<DialogActions
				style={{
					justifyContent: "flex-start",
				}}
			>
				{onOk && (
					<MasButton
						variant="contained"
						disableElevation
						color="primary"
						label=" "
						type="button"
						onClick={() => {
							handleOk();
						}}
					/>
				)}
				<MasButton
					variant="outlined"
					color="secondary"
					label=" "
					type="button"
					onClick={handleCancel}
				></MasButton>
			</DialogActions>
		</Dialog>
	);
};
