import {Dispatch} from "redux";
import {GetCtgs} from "../../../client/grpc-client";
import {showNotification} from "../../store";
import {CtgsDispatchTypes, CTGS_FAIL, CTGS_SUCSSES} from "./ctgsTypes";

export const getCtgs = () => {
	return async (dispatch: Dispatch<CtgsDispatchTypes>) => {
		try {
			let res = await GetCtgs();
			dispatch({
				type: CTGS_SUCSSES,
				payload: res,
			});
		} catch (e) {
			let err = e.Message || e.message || e.error || e.Exception || JSON.stringify(e);
			dispatch({
				type: CTGS_FAIL,
				payload: err,
			});
			showNotification(err, "error");
		}
	};
};
