import {Charge, Post} from "../../../client/grpc-client";
import {
	Bill,
	ChargeRequest,
	CONNECTION_STATUS_TYPE,
	CONNECTION_STATUS_TYPEMap,
	Ctg,
	Meter,
	MeterOperationStatus,
	MeterOperationStatusMap,
	PostMessage,
	READING_TYPE,
	ServiceReading,
	SERVICE_TYPE,
	SERVICE_TYPEMap,
} from "../../../proto/service_pb";
import {dateToTimeStamp} from "../../../client/converter";
import {UserData} from "../../../pages/recalc/recalc-item-cls";



export async function SendRequest({
	bilngDate,
	cycleLength,
	newBill,
	userData,
	oldBill,
}: {
	bilngDate: Date;
	cycleLength: number;
	userData: UserData;
	newBill: Bill.AsObject;
	oldBill: Bill.AsObject;
}): Promise<Bill.AsObject> {
	return new Promise<Bill.AsObject>((resolve, reject) => {
		let cst = newBill.customer;
		let property = cst.property;
		if (!property) {
			reject("  ");
			return;
		}
		let services = property?.servicesList || [];
		if (services.length === 0) {
			reject("      ");
			return;
		}
		let haveError = false;
		services.forEach((sv) => {
			if (!sv.connection) {
				reject("     ");
				haveError = true;
			}
		});
		if (haveError) {
			return;
		}
		// let oldConnStatus = services[0]?.connection?.connectionstatus;
		if (userData.isConnected === undefined) {
			reject("     ");
			return;
		}
		if (userData.haveMeter === undefined) {
			reject("     ");
			return;
		}

		let haveMeter = userData.haveMeter;
		let isConnected = userData.isConnected;
		let isSewer = userData.IsSewerConnection;
		if (haveMeter) {
			if (userData.ReadType === undefined || userData.ReadType == null) {
				reject("     ");
				return;
			}
		}

		let conStatus: CONNECTION_STATUS_TYPEMap[keyof CONNECTION_STATUS_TYPEMap] = null;
		let meterOpStatus: MeterOperationStatusMap[keyof MeterOperationStatusMap] = null;
		let readType = userData.ReadType;
		if (isConnected) {
			if (haveMeter) {
				conStatus = CONNECTION_STATUS_TYPE.CONNECTED_WITH_METER;
				if (readType === READING_TYPE.ACTUAL) {
					meterOpStatus = MeterOperationStatus.WORKING;
				} else {
					meterOpStatus = MeterOperationStatus.NOT_WORKING;
				}
				(newBill.customer.property.servicesList || []).forEach((sv) => {
					if (sv.connection && !sv.connection.meter) {
						let metr: Meter.AsObject = {
							metertype: "99",
							meterref: newBill.customer.custkey,
							diameter: userData.MeterDiameter,
							opstatus: meterOpStatus,
						};
						sv.connection.meter = metr;
					}
				});
			} else {
				conStatus = CONNECTION_STATUS_TYPE.CONNECTED_WITHOUT_METER;
			}
		} else {
			if (haveMeter) {
				conStatus = CONNECTION_STATUS_TYPE.DISCONNECTED_WITH_METER;
				if (readType === READING_TYPE.ACTUAL) {
					meterOpStatus = MeterOperationStatus.WORKING;
				} else {
					meterOpStatus = MeterOperationStatus.NOT_WORKING;
				}
			} else {
				conStatus = CONNECTION_STATUS_TYPE.DISCONNECTED_WITHOUT_METER;
			}
		}
		if (conStatus === undefined || conStatus == null) {
			reject("   ");
			return;
		}
		if (conStatus === undefined || conStatus == null || conStatus < 0) {
			reject("   ");
			return;
		}
		let serviceReadings: Array<ServiceReading.AsObject> = [];
		let mainCtg: Ctg.AsObject = {
			ctype: userData.MainCtype,
			ctypegroupid: userData.CtypeGroupId,
			description: " ",
			groupdescription: " ",
			tariffsList: [],
		};
		let servicesList: Array<SERVICE_TYPEMap[keyof SERVICE_TYPEMap]> = [];
		servicesList.push(SERVICE_TYPE.WATER);
		if (isSewer) {
			servicesList.push(SERVICE_TYPE.SEWER);
		}
		services.forEach((sv) => {
			let conn = sv.connection;
			conn.connectionstatus = conStatus as any;
			if (!haveMeter) {
				conn.meter = null;
				if (userData.ReadType === READING_TYPE.ACTUAL) {
					userData.ReadType = READING_TYPE.ESTIMATE;
				}
			} else {
				conn.meter = sv.connection.meter || {};
				conn.meter.diameter = userData.MeterDiameter;
				conn.meter.opstatus = meterOpStatus;
			}

			conn.ctype = mainCtg;
			conn.nounits = userData.MainNoUnits;
			if (userData.ReadType === READING_TYPE.ACTUAL) {
				serviceReadings.push({
					servicetype: sv.servicetype,
					reading: {
						consump: userData.Consumption,
						crreading: userData.CrReading,
						crdate: userData.CrDate
							? dateToTimeStamp(userData.CrDate, true).toObject()
							: null,
						prreading: userData.PrReading,
						prdate: userData.PrDate
							? dateToTimeStamp(userData.PrDate, true).toObject()
							: null,
						readtype: userData.ReadType,
					},
				});
			} else {
				console.log("estim cons:" + userData.Consumption);
				conn.estimcons = userData.Consumption;
			}
			conn.subconnectionsList = userData.SubConnections;
		});

		//fix debt and crdt notes
		let bilngDateTs = dateToTimeStamp(bilngDate, true).toObject();
		let o = {...oldBill};
		o.ftransactionsList = (o.ftransactionsList || []).filter(
			(a) => !(a.code === "DBT_AMT" || a.code === "CRDT_AMT" || a.code === "INSTALLS_AMT")
		);
		if (userData.DbtAmt > 0) {
			o.ftransactionsList.push({
				bilngdate: bilngDateTs,
				effdate: bilngDateTs,
				nounits: newBill.customer?.property?.servicesList[0]?.connection?.nounits,
				ctype: newBill.customer?.property?.servicesList[0]?.connection?.ctype,
				code: "DBT_AMT",
				amount: userData.DbtAmt,
				description: "",
				servicetype: newBill.customer?.property?.servicesList[0]?.servicetype,
			});
		}
		if (userData.CrdAmt < 0) {
			o.ftransactionsList.push({
				bilngdate: bilngDateTs,
				effdate: bilngDateTs,
				nounits: newBill.customer?.property?.servicesList[0]?.connection?.nounits,
				ctype: newBill.customer?.property?.servicesList[0]?.connection?.ctype,
				code: "CRDT_AMT",
				amount: userData.CrdAmt,
				description: "",
				servicetype: newBill.customer?.property?.servicesList[0]?.servicetype,
			});
		}
		if (userData.InstalsAmt > 0) {
			o.ftransactionsList.push({
				bilngdate: bilngDateTs,
				effdate: bilngDateTs,
				nounits: newBill.customer?.property?.servicesList[0]?.connection?.nounits,
				ctype: newBill.customer?.property?.servicesList[0]?.connection?.ctype,
				code: "INSTALLS_AMT",
				amount: userData.InstalsAmt,
				description: "",
				servicetype: newBill.customer?.property?.servicesList[0]?.servicetype,
			});
		}

		let re: ChargeRequest.AsObject = {
			customer: newBill.customer,
			setting: {
				bilingdate: bilngDateTs,
				cyclelength: cycleLength || 1,
				ignoretimeeffect: false,
			},
			servicesreadingsList: serviceReadings,
			servicesList: servicesList,
			oldbill: o,
		};
		console.log(re, "charge req");
		Charge(re)
			.then((d) => {
				resolve(d?.billsList[0]);
			})
			.catch((er) => {
				reject(er);
			});
	});
}

export async function PostBills(
	bills: Array<Bill.AsObject>,
	isCancelledRequest: boolean,
	formNo: number | null = null
): Promise<boolean> {
	let msg: PostMessage.AsObject = {
		data: {
			billsList: bills,
		},
	};
	msg.iscancelledrequest = isCancelledRequest;
	msg.cancelledrequestformno = formNo;
	return Post(msg);
}
