import React from "react";
import {
	Box,
	fade,
	FormControl,
	FormControlLabel,
	Grid,
	IconButton,
	makeStyles,
	Paper,
	Radio,
	RadioGroup,
	Typography,
} from "@material-ui/core";
import {MasInput} from "../../components/ui/MasInput";
import {MasSelect} from "../../components/ui/MasSelect";
import {MasButton} from "../../components/ui/MasButton";
import MasDataTable from "../../components/data-table";
import MasTooltip from "../../components/ui/MasTooltip";
import LinkRoundedIcon from "@material-ui/icons/LinkRounded";
import {ColumnProps} from "primereact/column";

export default function CancellBillsAction() {
	const classes = useStyles();
	const [value, setValue] = React.useState("billmenu");

	const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
		setValue((event.target as HTMLInputElement).value);
	};
	const renderHeader = (
		<div className="table-header">
			<Typography variant="h6" className="bold">
				{value === "billmenu" ? " " : " "}
			</Typography>
		</div>
	);
	const commentTemplete = (rowData: any) => {
		return (
			<>
				<MasTooltip title=" ">
					<IconButton aria-label="Edit" className={classes.circle}>
						<LinkRoundedIcon />
					</IconButton>
				</MasTooltip>
			</>
		);
	};

	const columns: ColumnProps[] = [
		{
			field: "id",
			header: " ",
			headerStyle: {width: "7%"},
		},
		{
			field: "code",
			header: " ",
		},
		{field: "category", header: " "},
		{field: "name", header: " "},
		{
			field: "category",
			header: "",
			body: commentTemplete,
			editor: commentTemplete,
			headerStyle: {width: "12%"},
		},
	];
	const columns2: ColumnProps[] = [
		{
			field: "id",
			header: " 	",
		},
		{
			field: "code",
			header: " ",
		},
		{field: "category", header: " "},
		{
			field: "description",
			header: "",
		},
	];
	return (
		<Grid container justify="center" direction="column" className={classes.wrapper}>
			<Grid container alignItems="center" className={classes.paegHeader}>
				<Typography variant="h5"></Typography>
			</Grid>
			<Paper className={classes.orderInfo}>
				<Box className={classes.orderInfo_title}>
					<Typography variant="h6" className="bold inline">
						  100
					</Typography>
					<Typography variant="subtitle1" className={classes.subtitle}>
						   
					</Typography>
				</Box>
				<Grid container spacing={1}>
					<Grid item xs={6}>
						<MasInput label=" " type="date" />
					</Grid>
					<Grid item xs={6}>
						<MasInput type="number" label=" " />
					</Grid>
					<Grid item xs={6}>
						<MasInput type="text" label="  " />
					</Grid>
					<Grid item xs={6}>
						<MasInput type="number" label=" " />
					</Grid>
					<Grid item xs={6}>
						<MasInput label="  " />
					</Grid>
					<Grid item xs={6}>
						<MasInput label="  " />
					</Grid>
					<Grid item xs={12}>
						<Typography className={classes.label} variant="subtitle1">
							
						</Typography>
						<MasSelect
							style={{marginLeft: "4px"}}
							items={Items}
							itemText="name"
							itemValue="name"
							fullWidth
						/>
					</Grid>
					<Grid xs={12}>
						<Box mt={2} mb={0.5} className="text-center">
							<MasButton
								styleBtn={classes.btn}
								variant="contained"
								label=""
								color="primary"
							/>
						</Box>
					</Grid>
				</Grid>
			</Paper>
			<Paper className={classes.dataTable}>
				<Box pt={1} pb={1}>
					<FormControl component="fieldset">
						<RadioGroup
							onChange={handleChange}
							row
							aria-label="position"
							name="position"
							value={value}
						>
							<FormControlLabel
								value="billmenu"
								control={<Radio color="secondary" />}
								label=" "
								labelPlacement="start"
							/>
							<FormControlLabel
								value="actionhist"
								control={<Radio color="secondary" />}
								label=" "
								labelPlacement="start"
							/>
						</RadioGroup>
					</FormControl>
				</Box>
				{value === "billmenu" ? (
					<MasDataTable header={renderHeader} data={products} columns={columns} />
				) : (
					<MasDataTable header={renderHeader} data={products} columns={columns2} />
				)}
			</Paper>
		</Grid>
	);
}

const useStyles = makeStyles((theme) => ({
	wrapper: {},
	paegHeader: {
		padding: theme.spacing(1),
		backgroundColor: "#fff",
		boxShadow: " 0px 10px 16px rgb(147 158 166 / 10%)",
		position: "relative",
		width: "100%",
		"& .MuiTypography-h5": {
			fontFamily: "DINMED",
			color: theme.palette.primary.main,
		},
	},
	orderInfo: {
		padding: theme.spacing(2),
		marginBottom: theme.spacing(1.5),
		marginTop: theme.spacing(1.5),
		width: "85%",
		alignSelf: "center",
	},
	orderInfo_title: {
		borderBottom: "1px solid #eee",
		marginBottom: theme.spacing(1),
		paddingBottom: theme.spacing(1),
	},
	subtitle: {
		color: theme.palette.grey[400],
	},
	label: {
		color: "#757575",
		margin: "0 0 6px 4px",
	},
	btn: {
		padding: "8px 24px",
	},
	dataTable: {
		width: "85%",
		alignSelf: "center",
		padding: "2px",
		background: "#fff",
		marginBottom: theme.spacing(2),
	},
	circle: {
		width: 35,
		height: 35,
		borderRadius: "50%",
		backgroundColor: theme.palette.primary.main,
		color: theme.palette.secondary.main,
		margin: "0 4px",
		"&:hover": {
			backgroundColor: fade(theme.palette.primary.main, 0.7),
			boxShadow: theme.shadows[2],
		},
	},
}));

const Items = [
	{id: "1", name: " "},
	{id: "2", name: "   "},
];

const products = [
	{
		id: "1000",
		code: "f230fh0g3",
		name: "Bamboo Watch",
		description: "Product Description",
		image: "bamboo-watch.jpg",
		price: 65,
		category: "Accessories",
		quantity: 24,
		inventoryStatus: "INSTOCK",
		rating: 5,
	},
	{
		id: "1001",
		code: "nvklal433",
		name: "Black Watch",
		description: "Product Description",
		image: "black-watch.jpg",
		price: 72,
		category: "Accessories",
		quantity: 61,
		inventoryStatus: "INSTOCK",
		rating: 4,
	},
];
