import {AlertProps} from "@material-ui/lab/Alert";

export const SHOW_NOTIFICATION = "SHOW_NOTIFICATION";
export const CLOSE_NOTIFICATION = "CLOSE_NOTIFICATION";

export interface Notifications {
	severity?: AlertProps["severity"];
	msg?: string;
	count?: number;
}
export interface NotificationSuccess {
	type: typeof SHOW_NOTIFICATION;
	payload: Notifications;
}

export interface NotificationClose {
	type: typeof CLOSE_NOTIFICATION;
}

export type NotificationTypes = NotificationSuccess | NotificationClose;

export const ShowNotification = (notification: Notifications) => {
	return {
		type: SHOW_NOTIFICATION,
		payload: notification,
	};
};

export const CloseNotification = () => {
	return {
		type: CLOSE_NOTIFICATION,
	};
};
