// @ts-nocheck
import {Timestamp} from "google-protobuf/google/protobuf/timestamp_pb";
import {
	Bill,
	BillResponce,
	CancelledRequest,
	ChargeRequest,
	ChargeSetting,
	Connection,
	Ctg,
	Customer,
	FinantialTransaction,
	MeasuredTransaction,
	Meter,
	PostMessage,
	Property,
	Reading,
	Service,
	ServiceReading,
	SubConnection,
} from "../proto/service_pb";

export function fromCtg(t: Ctg.AsObject): Ctg {
	if (t == null) {
		return null;
	}
	let m = new Ctg();
	m.setCtype(t.ctype);
	m.setCtypegroupid(t.ctypegroupid);
	m.setDescription(t.description || "???");
	m.setGroupdescription(t.groupdescription);
	m.setNoopEstimCons(t.noopEstimCons);
	m.setOpEstimCons(t.opEstimCons);
	console.log(t.ctype, t.description, m.getCtype(), m.getDescription());
	return m;
}

export function fromMeter(me: Meter.AsObject): Meter {
	if (me == null) {
		return null;
	}
	let m = new Meter();
	m.setConverterfactor(me.converterfactor);
	m.setDiameter(me.diameter);
	m.setMeterref(me.meterref);
	m.setMetertype(me.metertype);
	m.setOpstatus(me.opstatus);
	return m;
}

export function fromSubConnection(sub: SubConnection.AsObject): SubConnection {
	if (sub == null) {
		return null;
	}
	let s = new SubConnection();
	let cg = fromCtg(sub.ctype);
	s.setCtype(cg);

	s.setConsumptionpercentage(sub.consumptionpercentage);
	s.setEstimateconsumption(sub.estimateconsumption);
	s.setNounits(sub.nounits);
	s.getCtype();
	return s;
}

export function fromConnection(con: Connection.AsObject): Connection {
	if (con == null) {
		return null;
	}
	let c = new Connection();
	c.setConndiameter(con.conndiameter);
	c.setConnectionstatus(con.connectionstatus);
	c.setCtype(fromCtg(con.ctype));
	c.setIsbulkmeter(con.isbulkmeter);
	c.setMeter(fromMeter(con.meter));
	c.setNounits(con.nounits);
	c.setEstimcons(con.estimcons);
	let sb = (con.subconnectionsList || []).map((a) => fromSubConnection(a));
	c.setSubconnectionsList(sb);
	return c;
}

export function fromService(sv: Service.AsObject): Service {
	if (sv == null) {
		return null;
	}
	let c = new Service();
	c.setConnection(fromConnection(sv.connection));
	c.setServicetype(sv.servicetype);
	return c;
}

export function fromProperty(p: Property.AsObject): Property {
	if (p == null) {
		return null;
	}
	let c = new Property();
	c.setInfoflag1(p.infoflag1);
	c.setInfoflag2(p.infoflag2);
	c.setInfoflag3(p.infoflag3);
	c.setInfoflag4(p.infoflag4);
	c.setInfoflag5(p.infoflag5);
	c.setIsvacated(p.isvacated);
	c.setNorooms(p.norooms);
	c.setPropref(p.propref);
	c.setTownship(p.township);
	let slist = (p.servicesList || []).map((a) => fromService(a));
	c.setServicesList(slist);
	return c;
}

export function fromCustomer(t: Customer.AsObject): Customer {
	if (t == null) {
		return null;
	}
	let c = new Customer();
	c.setBillgroup(t.billgroup);
	c.setCustkey(t.custkey);
	c.setCusttype(t.custtype);
	c.setInfoflag1(t.infoflag1);
	c.setInfoflag2(t.infoflag2);
	c.setInfoflag3(t.infoflag3);
	c.setInfoflag4(t.infoflag4);
	c.setInfoflag5(t.infoflag5);
	c.setIscompany(t.iscompany);
	c.setProperty(fromProperty(t.property));
	return c;
}

export function fromTimestamp(t: Timestamp.AsObject): Timestamp {
	if (t == null) {
		return null;
	}
	let c = new Timestamp();
	c.setNanos(t.nanos);
	c.setSeconds(t.seconds);
	return c;
}

export function fromChargeSetting(t: ChargeSetting.AsObject): ChargeSetting {
	if (t == null) {
		return null;
	}
	let c = new ChargeSetting();
	c.setBilingdate(fromTimestamp(t.bilingdate));
	c.setCyclelength(t.cyclelength);
	c.setIgnoretimeeffect(t.ignoretimeeffect);
	return c;
}

export function fromReading(t: Reading.AsObject): Reading {
	if (t == null) {
		return null;
	}
	let c = new Reading();
	c.setConsump(t.consump);
	c.setCrdate(fromTimestamp(t.crdate));
	c.setCrreading(t.crreading);
	c.setPrdate(fromTimestamp(t.prdate));
	c.setPrreading(t.prreading);
	c.setReadingby(t.readingby);
	c.setReadtype(t.readtype);
	return c;
}

export function fromMeasuredTransaction(
	t: MeasuredTransaction.AsObject
): MeasuredTransaction {
	if (t == null) {
		return null;
	}
	let c = new MeasuredTransaction();
	c.setConsump(t.consump);
	c.setCrreading(t.crreading);
	c.setPrreading(t.prreading);
	c.setReadtype(t.readtype);
	c.setMeterref(t.meterref);
	c.setMetertype(t.metertype);
	c.setReadtype(t.readtype);
	return c;
}

export function fromFinantialTransaction(
	t: FinantialTransaction.AsObject
): FinantialTransaction {
	if (t == null) {
		return null;
	}
	let c = new FinantialTransaction();
	c.setAmount(t.amount);
	c.setCode(t.code);
	c.setDescription(t.description);
	c.setDiscountamount(t.discountamount);
	c.setEditable(t.editable);
	c.setEffdate(fromTimestamp(t.effdate));
	c.setTaxamount(t.taxamount);
	c.setNounits(t.nounits);
	c.setCtype(fromCtg(t.ctype));
	c.setPropref(t.propref);
	c.setConsumptionpercentage(t.consumptionpercentage);
	c.setTariffid(t.tariffid);
	c.setBilngdate(fromTimestamp(t.bilngdate));
	c.setMtransaction(fromMeasuredTransaction(t.mtransaction));
	c.setServicetype(t.servicetype);
	return c;
}

export function fromBill(t: Bill.AsObject): Bill {
	if (t == null) {
		return null;
	}
	let c = new Bill();
	c.setBilngdate(fromTimestamp(t.bilngdate));
	c.setCustomer(fromCustomer(t.customer));
	c.setFtransactionsList(t.ftransactionsList.map((a) => fromFinantialTransaction(a)));
	c.setServicesreadingsList(t.servicesreadingsList.map((a) => fromServiceReading(a)));
	c.setPaymentno(t.paymentno);
	c.setComment(t.comment);
	return c;
}

export function fromBillResponce(t: BillResponce.AsObject): BillResponce {
	if (t == null) {
		return null;
	}
	let c = new BillResponce();
	if (t.billsList) {
		c.setBillsList(t.billsList.map((a) => fromBill(a)));
	}
	if (t.recalcform) {
		let frm = new CancelledRequest();
		frm.setFormdate(fromTimestamp(t.recalcform.formdate));
		frm.setFormno(t.recalcform.formno);
		frm.setStatus(t.recalcform.status);
		c.setRecalcform(frm);
	}
	return c;
}

export function fromPostMessage(t: PostMessage.AsObject): PostMessage {
	if (t == null) {
		return null;
	}
	let c = new PostMessage();
	if (t.data) {
		c.setData(fromBillResponce(t.data));
	}
	c.setIscancelledrequest(t.iscancelledrequest);
	c.setCancelledrequestformno(t.cancelledrequestformno);
	return c;
}

export function fromServiceReading(t: ServiceReading.AsObject): ServiceReading {
	if (t == null) {
		return null;
	}
	let c = new ServiceReading();
	c.setServicetype(t.servicetype);
	c.setReading(fromReading(t.reading));
	return c;
}

export function fromChargeRequest(t: ChargeRequest.AsObject): ChargeRequest {
	if (t == null) {
		return null;
	}
	let c = new ChargeRequest();
	c.setCustomer(fromCustomer(t.customer));
	c.setSetting(fromChargeSetting(t.setting));
	let lst = (t.servicesreadingsList || []).map((a) => fromServiceReading(a));
	c.setServicesreadingsList(lst);
	c.setOldbill(fromBill(t.oldbill));
	let stl = (t.servicesList || []).map((a) => a);
	c.setServicesList(stl);
	return c;
}

export interface UtcDate {
	seconds: number;
	nanos: number;
}

export function dateToUtc(dt: Date, ignoreTime: boolean): number {
	if (dt == null) {
		return null;
	}
	if (ignoreTime) {
		return Date.UTC(dt.getFullYear(), dt.getMonth(), dt.getDate(), 0, 0, 0) / 1000;
	}
	return (
		Date.UTC(
			dt.getFullYear(),
			dt.getMonth(),
			dt.getDate(),
			dt.getHours(),
			dt.getMinutes(),
			dt.getSeconds()
		) / 1000
	);
}

export function dateToTimeStamp(dt: Date, ignoreTime): Timestamp {
	if (dt == null) {
		return null;
	}
	let t = new Timestamp();
	let seconds = dateToUtc(dt, ignoreTime);
	t.setSeconds(seconds);
	/*if(nanos &&(!isNaN(nanos))){
        t.setNanos(nanos);
    }*/
	return t;
}

export function timeStampToDate(stamp: Timestamp.AsObject): Date {
	if (stamp == null) {
		return null;
	}
	let s = new Timestamp();
	s.setNanos(stamp.nanos);
	s.setSeconds(stamp.seconds);
	return s.toDate();
}
