import React from "react";
import {fade, Grid, IconButton, makeStyles, Paper, Typography} from "@material-ui/core";
import {MasSelect} from "../../components/ui/MasSelect";
import {MasButton} from "../../components/ui/MasButton";
import {Link} from "react-router-dom";
import MasDataTable from "../../components/data-table";
import CreateRoundedIcon from "@material-ui/icons/CreateRounded";
import LinkRoundedIcon from "@material-ui/icons/LinkRounded";
import clsx from "clsx";
import MasTooltip from "../../components/ui/MasTooltip";
import {ColumnProps} from "primereact/column";

export default function CancellBills() {
	const classes = useStyles();

	const commentTemplete = (rowData: any) => {
		return (
			<>
				<MasTooltip title="">
					<Link to="/cancellBillsAction">
						<IconButton aria-label="Edit" className={classes.circle}>
							<CreateRoundedIcon />
						</IconButton>
					</Link>
				</MasTooltip>
				<MasTooltip title="">
					<IconButton
						aria-label="delete"
						className={clsx(classes.circle, classes.circle2)}
					>
						<LinkRoundedIcon />
					</IconButton>
				</MasTooltip>
			</>
		);
	};

	const columns: ColumnProps[] = [
		{
			field: "id",
			header: " ",
			headerStyle: {width: "7%"},
		},
		{
			field: "code",
			header: " ",
		},
		{field: "category", header: " "},
		{field: "name", header: " "},
		{
			field: "category",
			header: "",
			body: commentTemplete,
			editor: commentTemplete,
			headerStyle: {width: "12%"},
		},
	];

	return (
		<Grid
			container
			justify="center"
			alignItems="center"
			direction="column"
			className={classes.wrapper}
		>
			<Grid item className={classes.paegHeader}>
				<Typography style={{textAlign: "center"}} variant="h5">
					 
				</Typography>
			</Grid>
			<Paper elevation={1} className={classes.section}>
				<Grid container justify="space-between" alignItems="center">
					<Grid item>
						<MasSelect
							style={{marginLeft: "4px"}}
							items={Items}
							itemText="name"
							itemValue="name"
							fullWidth={false}
						/>
						<MasButton
							disableElevation
							variant="contained"
							label=""
							type="button"
							color="secondary"
						/>
					</Grid>
					<Grid item>
						<Link to="/cancellBillsRequest">
							<MasButton
								disableElevation
								variant="contained"
								label=" "
								type="button"
								color="primary"
							/>
						</Link>

						<MasButton
							disableElevation
							variant="outlined"
							label=""
							type="button"
							color="primary"
						/>
					</Grid>
				</Grid>
			</Paper>
			<Paper className={classes.table}>
				<MasDataTable data={products} columns={columns} />
			</Paper>
		</Grid>
	);
}

const useStyles = makeStyles((theme) => ({
	wrapper: {},
	paegHeader: {
		padding: theme.spacing(1.2),
		backgroundColor: "#fff",
		// boxShadow: " 0px 3px 10px rgb(147 158 166 / 10%)",
		borderBottom: "1px solid #eee",
		position: "relative",
		width: "100%",
		justifyContent: "center",
		"& .MuiTypography-h5": {
			fontFamily: "DINMED",
			color: theme.palette.primary.main,
		},
	},
	section: {
		marginTop: theme.spacing(2),
		width: "85%",
		padding: 16,
	},
	table: {
		marginTop: 16,
		width: "85%",
	},
	circle: {
		width: 35,
		height: 35,
		borderRadius: "50%",
		backgroundColor: theme.palette.primary.main,
		color: theme.palette.secondary.main,
		margin: "0 4px",
		"&:hover": {
			backgroundColor: fade(theme.palette.primary.main, 0.7),
			boxShadow: theme.shadows[2],
		},
	},
	circle2: {
		backgroundColor: theme.palette.secondary.main,
		color: theme.palette.primary.main,
		"&:hover": {
			backgroundColor: fade(theme.palette.secondary.main, 0.7),
			boxShadow: theme.shadows[2],
		},
	},
}));

