import React from "react";
import {
	createStyles,
	Theme,
	FormControl,
	InputBase,
	withStyles,
	Typography,
	makeStyles,
} from "@material-ui/core";

interface props {
	styleLabel?: any;
	styleInput?: any;
	styleFormControl?: any;
	error?: any;
	className?: any;
	autoComplete?: string;
	labelWidth?: number;
	required?: boolean;
	type?: string;
	id?: string;
	label?: string;
	inputRef?: any;
	name?: string;
	endAdornment?: any;
	value?: string;
	multiline?: boolean;
	onChange?: (evt: any) => void;
	placeholder?: string;
	fullWidth?: boolean;
	rows?: string;
	rowsMax?: number;
	variant?: "outlined" | "standard" | "filled";
}

const useStyles = makeStyles((theme) => ({
	label: {
		margin: "0 4px 6px 0",
		color: theme.palette.grey[600],
	},
}));

const BootInput = withStyles((theme: Theme) =>
	createStyles({
		root: {
			"label + &": {
				marginTop: theme.spacing(3),
			},
		},
		input: {
			borderRadius: 4,
			position: "relative",
			backgroundColor: theme.palette.common.white,
			border: "1px solid #ced4da",
			fontSize: "1rem",
			width: "100%",
			padding: theme.spacing(1.8),
			transition: theme.transitions.create(["border-color", "box-shadow"]),
			"&:focus": {
				boxShadow: "0 0 0 0.2rem #a6d5fa",
				borderColor: "#2196F3",
			},
		},
	})
)(InputBase);

export function MasInput(props: props) {
	const {
		styleLabel,
		styleInput,
		styleFormControl,
		error,
		className,
		endAdornment,
		autoComplete,
		labelWidth,
		required,
		type,
		id,
		label,
		inputRef,
		name,
		value,
		multiline,
		onChange,
		placeholder,
		variant,
		fullWidth,
		rows,
		rowsMax,
		...InputProps
	} = props;
	const classes = useStyles();
	return (
		<FormControl variant="filled" fullWidth className={styleFormControl}>
			{label && (
				<Typography variant="subtitle1" className={classes.label}>
					{label}
				</Typography>
			)}
			<BootInput
				id={id}
				name={name}
				className={styleInput}
				autoComplete={autoComplete}
				inputRef={inputRef}
				type={type}
				error={error}
				multiline={multiline}
				rows={rows}
				rowsMax={rowsMax}
				value={value}
				placeholder={placeholder}
				onChange={onChange}
				{...InputProps}
			/>
		</FormControl>
	);
}
