import React, {useState} from "react";
import {makeStyles} from "@material-ui/core/styles";
import {
	AppBar,
	Toolbar,
	IconButton,
	Avatar,
	Typography,
	Box,
	Paper,
	ListItem,
	List,
	ListItemIcon,
	Divider,
	ListItemText,
} from "@material-ui/core";
import clsx from "clsx";
import Badge from "@material-ui/core/Badge";
import MenuItem from "@material-ui/core/MenuItem";
import Menu from "@material-ui/core/Menu";
import AccountCircle from "@material-ui/icons/AccountCircle";
import MailIcon from "@material-ui/icons/Mail";
import MoreIcon from "@material-ui/icons/MoreVert";
import MasTooltip from "../components/ui/MasTooltip";
import {NotificationsNoneRounded} from "@material-ui/icons";
import {MenuIcon, LogOutIcon} from "../components/ui/mas-icons";
import User from "../assets/images/vector/icons/user.svg";
import {useDispatch} from "react-redux";
import {
	KeyboardArrowDownRounded,
	FullscreenExitRounded,
	Brightness2Outlined,
	LockOutlined,
	PersonOutlineRounded,
} from "@material-ui/icons";
import {SignOut} from "../redux/actions/login/loginAction";
interface headerProps {
	title?: string;
	drawerWidth: string;
	titleStyle: any;
	open: boolean;
	handleDrawerOpen: any;
}
// const x = (props: headerProps) => props.drawerWidth;

export default function Header(props: headerProps) {
	const {open, handleDrawerOpen} = props;
	const classes = useStyles(props);
	const [mobileMoreAnchorEl, setMobileMoreAnchorEl] = useState(null);
	const [showProfileMenu, setShowProfileMenu] = useState<boolean>(false);
	const isMobileMenuOpen = Boolean(mobileMoreAnchorEl);
	const dispatch = useDispatch();

	const handleProfileMenuOpen = () => {
		setShowProfileMenu(!showProfileMenu);
	};

	const handleMobileMenuClose = () => {
		setMobileMoreAnchorEl(null);
	};

	const handleMobileMenuOpen = (event: any) => {
		setMobileMoreAnchorEl(event.currentTarget);
	};

	const mobileMenuId = "primary-search-account-menu-mobile";
	const renderMobileMenu = (
		<Menu
			anchorEl={mobileMoreAnchorEl}
			disableScrollLock={true}
			anchorOrigin={{vertical: "top", horizontal: "right"}}
			id={mobileMenuId}
			keepMounted
			transformOrigin={{vertical: "top", horizontal: "right"}}
			open={isMobileMenuOpen}
			onClose={handleMobileMenuClose}
		>
			<MenuItem>
				<IconButton aria-label="show 4 new mails" color="inherit">
					<Badge color="secondary">
						<MailIcon className={classes.icons} />
					</Badge>
				</IconButton>
				<p></p>
			</MenuItem>
			<MenuItem onClick={handleProfileMenuOpen}>
				<IconButton
					aria-label="account of current user"
					aria-controls="primary-search-account-menu"
					aria-haspopup="true"
					color="inherit"
				>
					<AccountCircle />
				</IconButton>
				<p> </p>
			</MenuItem>
		</Menu>
	);

	const handelSignOut = () => {
		dispatch(SignOut());
	};
	return (
		<>
			<AppBar
				position="fixed"
				color="primary"
				elevation={0}
				className={clsx(classes.appBar, {[classes.appBarShift]: open})}
			>
				<Toolbar className={classes.toolbar}>
					<IconButton
						color="inherit"
						aria-label="open drawer"
						onClick={handleDrawerOpen}
						edge="start"
						className={clsx(classes.menuButton, {[classes.hide]: open})}
					>
						<MenuIcon className={classes.icons} />
					</IconButton>

					<Box position="relative" top="-2px">
						<Typography className={classes.title} variant="h5" color="initial">
							      
						</Typography>
						<Typography className={classes.subTitle} variant="h6" color="initial">
							(   )
						</Typography>
					</Box>
					<div className={classes.grow} />
					<div className={classes.sectionDesktop}>
						<MasTooltip title="">
							<IconButton
								className={classes.IconButton}
								aria-label="notification"
								color="inherit"
							>
								<Badge color="secondary" badgeContent={4}>
									<NotificationsNoneRounded className={classes.icons} />
								</Badge>
							</IconButton>
						</MasTooltip>
						<MasTooltip title=" ">
							<IconButton
								className={classes.IconButton}
								aria-label="darkmode"
								color="inherit"
							>
								<Brightness2Outlined
									style={{
										transform: "rotate(34deg)",
										fontSize: "29px",
									}}
									className={classes.icons}
								/>
							</IconButton>
						</MasTooltip>
						<MasTooltip title=" ">
							<IconButton
								aria-haspopup="true"
								color="inherit"
								className={classes.IconButton}
							>
								<FullscreenExitRounded
									style={{
										fontSize: "34px",
									}}
									className={classes.icons}
								/>
							</IconButton>
						</MasTooltip>
						<Avatar variant="rounded" className={classes.userProfile}>
							<img src={User} alt="user" height="24px" />
						</Avatar>
						<Box
							display="flex"
							alignItems="center"
							justifyContent="center"
							marginLeft={1}
							marginRight={2}
							position="relative"
							className={classes.user}
						>
							<Typography className={classes.userName} variant="subtitle1">
								Mohamed
							</Typography>
							<IconButton
								disableTouchRipple={true}
								disableRipple={true}
								color="inherit"
								aria-label="user"
								onClick={handleProfileMenuOpen}
								edge="start"
								className={classes.userProfileIcon}
							>
								<KeyboardArrowDownRounded />
							</IconButton>
							<Paper className={classes.profileMenu} elevation={4}>
								<List
									style={{
										padding: "0",
									}}
									component="nav"
									aria-label="main profile "
								>
									<ListItem
										classes={{
											root: classes.listItem,
										}}
										button
										disableGutters
									>
										<ListItemIcon
											style={{
												minWidth: "36px",
												color: "#191B1B",
											}}
										>
											<PersonOutlineRounded />
										</ListItemIcon>
										<ListItemText primary=" " />
									</ListItem>
									<Divider />
									<ListItem
										classes={{
											root: classes.listItem,
										}}
										button
										disableGutters
										onClick={handelSignOut}
									>
										<ListItemIcon
											style={{
												minWidth: "36px",
												color: "#191B1B",
											}}
										>
											<LogOutIcon
												style={{
													fontSize: "21px",
													margin: "0 5px",
												}}
											/>
										</ListItemIcon>
										<ListItemText primary=" " />
									</ListItem>
									<Divider />
									<ListItem
										classes={{
											root: classes.listItem,
										}}
										button
										disableGutters
									>
										<ListItemIcon
											style={{
												minWidth: "36px",
												color: "#191B1B",
											}}
										>
											<LockOutlined />
										</ListItemIcon>
										<ListItemText primary="  " />
									</ListItem>
								</List>
							</Paper>
						</Box>
					</div>
					<div className={classes.sectionMobile}>
						<IconButton
							aria-label="show more"
							aria-controls={mobileMenuId}
							aria-haspopup="true"
							onClick={handleMobileMenuOpen}
							color="inherit"
							className={classes.IconButton}
						>
							<MoreIcon className={classes.icons} />
						</IconButton>
					</div>
				</Toolbar>
			</AppBar>
			{renderMobileMenu}
		</>
	);
}
const useStyles = makeStyles((theme) => ({
	appBar: {
		padding: "4px !important",
		color: theme.palette.text.primary,
		boxShadow:
			"0 0.4rem 2.1875rem rgba(4,9,20,0.03), 0 0.7rem 1.40625rem rgba(4,9,20,0.03), 0 0.15rem 0.53125rem rgba(4,9,20,0.05), 0 0.125rem 0.1875rem rgba(4,9,20,0.03)",
		zIndex: theme.zIndex.drawer + 1,
		transition: "all 0.25s ease-out",
		background: "#fff",
	},
	appBarShift: {
		// marginRight: "260px",
		width: `calc(100% - 250px)`, // need to convert props.drawerWidth
		transition: "all 0.2s ease",
	},
	grow: {
		flexGrow: 1,
	},
	menuButton: {
		marginRight: 28,
		padding: "0 6px",
	},
	hide: {
		display: "none",
	},
	sectionDesktop: {
		display: "none",
		justifyContent: "center",
		alignItems: "center",
		[theme.breakpoints.up("md")]: {
			display: "flex",
		},
	},
	sectionMobile: {
		display: "flex",
		[theme.breakpoints.up("md")]: {
			display: "none",
		},
	},
	title: {
		fontFamily: "DINMED",
		display: "inline-block",
	},
	icons: {
		fontSize: "30px",
	},
	subTitle: {
		display: "inline-block",
		margin: "0 8px",
		color: theme.palette.primary.main,
		fontFamily: "DINREG",
	},
	toolbar: {
		[theme.breakpoints.down("md")]: {
			minHeight: "64px",
		},
	},
	IconButton: {
		padding: theme.spacing(1),
	},
	user: {
		"&:hover": {
			"&  $profileMenu": {
				opacity: 100,
				transform: "translateY(0)",
				visibility: "visible",
			},
		},
	},
	userProfile: {
		color: "#fff",
		backgroundColor: theme.palette.primary.main,
		backgroundImage: "linear-gradient( 108.1deg,  #ffe9a5 16.2%, #63e8c3 88.9% )",
		margin: "0 4px",
	},
	userName: {
		fontFamily: "DINMED , Arila",
	},
	userProfileIcon: {
		top: 2,
		"&:hover": {
			background: "none",
		},
	},
	profileMenu: {
		position: "absolute",
		minWidth: "160px",
		borderRadius: "6px",
		transition: "all 0.3s cubic-bezier(0.4, 0, 1, 1)",
		top: "60px",
		opacity: 0,
		visibility: "hidden",
		transform: "translateY(30px)",
		zIndex: 11,
		overflow: "hidden",
	},
	listItem: {
		padding: "8px 12px",
		"& .MuiListItemText-primary": {
			fontSize: "0.9rem",
			fontFamily: "DINMED",
		},
	},
}));
