import React from "react";
import {DataTable, DataTableSelectionChangeParams} from "primereact/datatable";
import {Column, ColumnProps} from "primereact/column";
// import {makeStyles, Typography} from "@material-ui/core";
import "primereact/resources/themes/saga-blue/theme.css";
import "primereact/resources/primereact.min.css";
import "primeicons/primeicons.css";
import "./DataTableDemo.css";

interface Props {
	data: any[];
	columns: ColumnProps[];
	header?: React.ReactNode;
	onSelectionChange?: (e: DataTableSelectionChangeParams) => void;
	selection?: any;
}

export default function MasDataTable(props: Props) {
	const {data, columns, header, onSelectionChange, selection} = props;
	const defaultCell = (props: any, field: string) => {
		console.log(props);
		return props.rowData[field];
	};

	const dynamicColumns = columns.map((colm, i) => {
		return (
			<Column
				key={i}
				selectionMode={colm.selectionMode}
				headerStyle={colm.headerStyle}
				field={colm.field}
				header={colm.header}
				headerClassName={colm.headerClassName}
				body={colm.body}
				editor={(props) =>
					colm.editor && colm.editor !== null
						? colm.editor(props)
						: defaultCell(props, colm.field || "")
				}
			/>
		);
	});

	return (
		<div className="datatable-doc-demo">
			<DataTable
				value={data}
				selection={selection}
				onSelectionChange={onSelectionChange}
				dataKey="id"
				editMode="cell"
				className="p-datatable-gridlines p-datatable-sm p-datatable-striped"
				header={header}
			>
				{dynamicColumns}
			</DataTable>
		</div>
	);
}
