import {LoginResponce} from "../../../proto/service_pb";

export const LOGIN_SUCCESS = "LOGIN_SUCSESS";
export const LOGIN_FAIL = "LOGIN_FAIL";
export const LOG_OUT = "LOG_OUT";

export interface LoginFail {
	type: typeof LOGIN_FAIL;
	payload: string;
}

export interface LoginSuccess {
	type: typeof LOGIN_SUCCESS;
	payload: LoginResponce.AsObject;
}
export interface SignOut {
	type: typeof LOG_OUT;
}

export type LoginDispatchTypes = LoginFail | LoginSuccess | SignOut;
