import React from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import Stepper from "@material-ui/core/Stepper";
import Step from "@material-ui/core/Step";
import StepButton from "@material-ui/core/StepButton";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import {CircularProgress, Snackbar, StepLabel} from "@material-ui/core";
import KeyboardArrowLeft from "@material-ui/icons/KeyboardArrowLeft";
import KeyboardArrowRight from "@material-ui/icons/KeyboardArrowRight";
import {Bill} from "../../proto/service_pb";
import Bills from "../../components/bills";
import MasAlertDialog from "../../components/ui/mas-alertDialog";
import MuiAlert, {AlertProps} from "@material-ui/lab/Alert";
import {DataItem} from "./recalc";

function Alert(props: AlertProps) {
	return <MuiAlert elevation={6} variant="filled" {...props} />;
}

interface StepperProps {
	executBill: boolean;
	bills: Array<DataItem>;
	onConfirm: (bill: Bill.AsObject) => Promise<any>;
}
export interface Notification {
	sevirity?: "error" | "success";
	open?: boolean;
	msg?: string;
}
export default function MasStepper(props: StepperProps) {
	const classes = useStyles();
	const {bills, executBill} = props;
	const [open, setOpen] = React.useState<boolean>(false);
	const [excute, setExcute] = React.useState<boolean>(false);
	const [activeStep, setActiveStep] = React.useState(0);
	const [completed] = React.useState<{[k: number]: boolean}>({});
	const [loading, setLoading] = React.useState<boolean>(false);
	const [show, setShow] = React.useState<Notification>({});
	const steps = getSteps();

	function getSteps() {
		return (bills || []).map((a) => {
			return a?.newBill?.bilngdate?.seconds;
		});
	}
	function getStepContent(step: number) {
		const itm = bills[step];
		console.log(itm.newBill, "new bill from stepper");
		console.log(itm.oldBill, "old bill from stepper");

		return (
			<Bills newBill={itm.newBill} calculated={itm.calculated} oldBill={itm.oldBill} />
		);
	}
	const totalSteps = () => {
		return steps.length;
	};
	const completedSteps = () => {
		return Object.keys(completed).length;
	};
	const isLastStep = () => {
		return activeStep === totalSteps() - 1;
	};
	const allStepsCompleted = () => {
		return completedSteps() === totalSteps();
	};
	const handleClose = () => {
		setOpen(false);
	};
	const handleNext = () => {
		const newActiveStep =
			isLastStep() && !allStepsCompleted()
				? // It's the last step, but not all steps have been completed,
				  // find the first step that has been completed
				  steps.findIndex((step, i) => !(i in completed))
				: activeStep + 1;
		setActiveStep(newActiveStep);
	};
	const handleBack = () => {
		setActiveStep((prevActiveStep) => prevActiveStep - 1);
	};
	const handleStep = (step: number) => () => {
		setActiveStep(step);
	};
	const handleComplete = () => {
		setOpen(true);
	};
	const handelConfirm = () => {
		setOpen(false);
		setLoading(true);
		if (props.onConfirm) {
			props
				.onConfirm(bills[0].newBill)
				.then(() => {
					setExcute(true);
					setLoading(false);
					setShow({
						...show,
						open: true,
						msg: "   ",
						sevirity: "success",
					});
				})
				.catch((ex) => {
					let msg = "error";
					if (ex?.toString) {
						msg = ex.toString();
					} else {
						if (typeof ex == "string") {
							msg = ex;
						}
						if (typeof ex == "object") {
							msg =
								ex.GrpcError ||
								ex.Error ||
								ex.error ||
								ex.Exception ||
								JSON.stringify(ex);
						}
					}
					setLoading(false);
					setShow({
						...show,
						open: true,
						msg: msg,
						sevirity: "error",
					});
				});
		}
	};

	if (loading) {
		return <CircularProgress></CircularProgress>;
	}

	return (
		<div className={classes.root}>
			{loading ? (
				<CircularProgress></CircularProgress>
			) : (
				<>
					<MasAlertDialog
						handelConfirm={handelConfirm}
						isOpen={open}
						onClose={handleClose}
					/>
					<Stepper
						alternativeLabel
						nonLinear
						className={classes.steper}
						//connector={<QontoConnector />}
						activeStep={activeStep}
					>
						{steps.map((label, index) => (
							<Step key={index}>
								<StepButton
									disableRipple
									onClick={handleStep(index)}
									completed={completed[index]}
								>
									{/* <StepLabel StepIconComponent={QontoStepIcon}>{label}</StepLabel> */}
									<StepLabel></StepLabel>
								</StepButton>
							</Step>
						))}
					</Stepper>
					<div>
						<div className={classes.instructions}>{getStepContent(activeStep)}</div>
						<div className={classes.stpFooter}>
							{bills?.length > 1 && (
								<div>
									<Button
										disabled={activeStep === 0}
										onClick={handleBack}
										className={classes.button}
									>
										<KeyboardArrowRight />
										<span style={{fontSize: "0.9rem"}}></span>
									</Button>
									<Button onClick={handleNext} className={classes.button}>
										<span style={{fontSize: "0.9rem"}}></span>
										<KeyboardArrowLeft />
									</Button>
								</div>
							)}
							{activeStep !== steps.length &&
								(completed[activeStep] ? (
									<Typography variant="subtitle2" className={classes.completed}>
										 
										<span style={{padding: "0 4px"}}>{activeStep + 1}</span>  
										
									</Typography>
								) : (
									<Button
										disabled={executBill ? excute : true}
										variant="contained"
										disableElevation
										color="secondary"
										type="submit"
										style={{padding: "8px 24px", fontSize: "1rem"}}
										onClick={handleComplete}
									>
										 
									</Button>
								))}
						</div>
					</div>
				</>
			)}
			<Snackbar
				open={show.open}
				autoHideDuration={4000}
				onClose={() => {
					setShow({...show, open: false});
				}}
			>
				<Alert
					onClose={() => {
						setShow({...show, open: false});
					}}
					severity={show.sevirity}
				>
					{show.msg}
				</Alert>
			</Snackbar>
		</div>
	);
}

const useStyles = makeStyles((theme: Theme) =>
	createStyles({
		root: {
			width: "100%",
		},
		button: {
			marginRight: theme.spacing(1),
			color: theme.palette.text.primary,
		},
		backButton: {
			marginRight: theme.spacing(1),
		},
		completed: {
			display: "inline-block",
			color: theme.palette.secondary.main,
		},
		instructions: {
			background: "#e9e9e9",
			padding: 16,
		},
		stpFooter: {
			display: "flex",
			justifyContent: "space-between",
			padding: "8px 12px",
			borderTop: "1px solid #ccc",
			alignItems: "center",
		},
		steper: {
			padding: "8px",
			borderBottom: "1px solid #ccc",
		},
		conform: {
			height: "250px",
			padding: "8px",
		},
	})
);
