export const POKEMMON_LOADING = "POKEMMON_LOADING";
export const POKEMMON_SUCCESS = "POKEMMON_SUCSESS";
export const POKEMMON_FAIL = "POKEMMON_FAIL";

export type PokemonTypes = {
	abilities: PokemoneAbility[];
	sprites: PokemoneSprites;
	stat: PokemoneStat[];
};

type PokemoneAbility = {
	ability: {
		name: string;
		url: string;
	};
};

type PokemoneSprites = {
	front_default: string;
};

type PokemoneStat = {
	base_stat: number;
	stat: {
		name: "string";
	};
};

export interface PokemonLoading {
	type: typeof POKEMMON_LOADING;
}
export interface PokemonFail {
	type: typeof POKEMMON_FAIL;
	payload: string;
}

export interface PokemonSuccess {
	type: typeof POKEMMON_SUCCESS;
	payload: PokemonTypes;
}

export type PokemonDispatchTypes = PokemonLoading | PokemonFail | PokemonSuccess;
