import React from "react";
import clsx from "clsx";
import {makeStyles, TextField, TextFieldProps} from "@material-ui/core";

const useStyles = makeStyles(() => ({
	root: {
		// marginBottom: "12px",
	},
}));

export type MasTextFieldPropsType = TextFieldProps & {
	styleTextFiled?: string;
};

export function MasTextField(_props: MasTextFieldPropsType) {
	const classes = useStyles();
	const {styleTextFiled, ...restProps} = _props;
	return (
		<TextField
			className={clsx(classes.root, [styleTextFiled])}
			variant="outlined"
			{...restProps}
		/>
	);
}