import React from "react";
import clsx from "clsx";
import {makeStyles, Theme, useTheme} from "@material-ui/core/styles";
import {Divider, Drawer, IconButton, Typography, fade} from "@material-ui/core";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";
import {MasList} from ".././components/ui/MasList";
import DashboardRoundedIcon from "@material-ui/icons/DashboardRounded";

import {
	SettingsRounded,
	Assessment,
	DeleteForeverRounded,
	ScannerRounded,
	DescriptionRounded,
	ExploreRounded,
} from "@material-ui/icons";
import {EmailSearch} from "../components/ui/mas-icons";
interface DrawerProps {
	drawerWidth: string;
	open: boolean;
	handleDrawerClose: any;
}

const useStyles = makeStyles((theme: Theme) => ({
	drawer: {
		width: (props: DrawerProps) => props.drawerWidth,
		flexShrink: 0,
		whiteSpace: "nowrap",
		"& .MuiDrawer-paper": {
			minHeight: "85vh",
			boxShadow: theme.m_shdows[0],
			backgroundColor: theme.palette.primary.main,
			"&::-webkit-scrollbar": {
				width: "0.55rem",
				height: "0px",
			},
			"&::-webkit-scrollbar-track": {
				"-webkit-box-shadow": "inset 0 0 6px rgba(0,0,0,0.00)",
			},
			"&::-webkit-scrollbar-thumb": {
				backgroundColor: "#7b8189",
				outline: "1px solid slategrey",
				height: "5px",
			},
		},
		"& .MuiDrawer-paperAnchorDockedRight": {
			// borderRight: "1px solid #cdcdce",
		},
	},
	drawerOpen: {
		width: (props: DrawerProps) => props.drawerWidth,
		transition: "all 0.2s ease-out",
	},
	drawerClose: {
		overflowX: "hidden",
		transition: "all 0.2s ease-out",
		width: theme.spacing(9),
	},
	toolbar: {
		display: "flex",
		alignItems: "center",
		justifyContent: "space-between",
		padding: theme.spacing(1.3, 1),
		background: "#fff",
		// necessary for content to be below app bar
		...theme.mixins.toolbar,
	},
	logo: {
		display: "flex",
		justifyContent: "center",
		alignItems: "center",
		"& h6": {
			padding: "0 8px",
			fontFamily: "DINMED",
		},
	},
	listStyle: {
		"&.MuiList-padding": {
			padding: "4px 0 4px 4px",
		},
	},
	textStyle: {
		"& *": {
			fontSize: "1.24rem",
		},
	},
	iconStyle: {
		fontSize: "28px",
		color: theme.palette.icon.main,
	},
	itemStyle: {
		borderRadius: "24px 0 0 24px",
		"&:hover": {
			background: theme.palette.primary.light,
		},
		"&.Mui-selected": {
			"&:hover": {
				background: fade(theme.palette.primary.main, 0.8),
			},
			background: theme.palette.primary.main,
			"& .MuiListItemIcon-root": {
				color: "#fff",
				transition: "color 0.3s ease",
			},
			"& .MuiListItemText-root": {
				color: "#fff",
				transition: "color 0.3s ease",
			},
		},
	},
	linkStyle: {
		textDecoration: "none",
		color: "inherit",
	},
}));

const items = [
	{id: 1, Name: " ", to: "/"},
	{id: 2, Name: " ", to: "/"},
	{id: 3, Name: " ", to: "/recalc"},
	{id: 4, Name: "", to: "/cancellBills"},
	// {id: 5, Name: "browser", to: "/browser"},
	// {id: 6, Name: "batchScan", to: "/batch"},
	// {id: 7, Name: "trash", to: "/trash"},
	// {id: 8, Name: "qr code", to: "/qrcode"},
	// {id: 9, Name: "", to: "/reports"},
	// {id: 10, Name: "workflow", to: "/workflow"},
	// {id: 11, Name: " ", to: "/workflowSearch"},
];
const icons = [
	<DashboardRoundedIcon fontSize="inherit" />,
	<SettingsRounded fontSize="inherit" />,
	<DescriptionRounded fontSize="inherit" />,
	<ExploreRounded fontSize="inherit" />,
	<ScannerRounded fontSize="inherit" />,
	<DeleteForeverRounded fontSize="inherit" />,
	<Assessment fontSize="inherit" />,
	<EmailSearch fontSize="inherit" />,
];

export default function MasDrawer(props: DrawerProps) {
	const {open, handleDrawerClose} = props;
	const classes = useStyles(props);
	const theme = useTheme();
	return (
		<Drawer
			variant="permanent"
			elevation={1}
			className={clsx(classes.drawer, {
				[classes.drawerOpen]: open,
				[classes.drawerClose]: !open,
			})}
			classes={{
				paper: clsx({
					[classes.drawerOpen]: open,
					[classes.drawerClose]: !open,
				}),
			}}
		>
			<div className={classes.toolbar}>
				<div className={classes.logo}>
					{/* <img alt="logo" width="50px" /> */}
					<Typography variant="subtitle1" color="initial">
						MASHHu
					</Typography>
				</div>
				<IconButton onClick={handleDrawerClose}>
					{theme.direction === "rtl" ? <ChevronRightIcon /> : <ChevronLeftIcon />}
				</IconButton>
			</div>
			<Divider />
			<MasList
				listStyle={classes.listStyle}
				linkStyle={classes.linkStyle}
				itemStyle={classes.itemStyle}
				iconStyle={classes.iconStyle}
				textStyle={classes.textStyle}
				name="list"
				items={items}
				primaryItemText="Name"
				itemValue="id"
				icons={icons}
			/>
		</Drawer>
	);
}
