import React from "react";
import {Theme, withStyles, fade} from "@material-ui/core/styles";
import MuiDialogTitle from "@material-ui/core/DialogTitle";
import MuiDialogContent from "@material-ui/core/DialogContent";
import MuiDialogActions from "@material-ui/core/DialogActions";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import {makeStyles, Typography} from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  dialog: {
    background: "linear-gradient( -160deg, #ffffff 35%, #bfcbca  100%)",
  },
  textHero: {
    fontFamily: "dorid-bld, sans-serif",
    color: theme.palette.text.primary,
  },
  desc: {
    color: "#e94f00",
    margin: theme.spacing(1.5, 0),
    fontFamily: "dorid-bld, sans-serif",
    fontSize: "0.7rem",
  },
}));

const DialogTitle = withStyles((theme: Theme) => ({
  root: {
    padding: theme.spacing(1, 2),
    borderBottom: `1px solid ${fade("#000", 0.1)}`,
    fontSize: "1rem",
  },
}))(MuiDialogTitle);

const DialogContent = withStyles((theme: Theme) => ({
  root: {
    padding: theme.spacing(3),
  },
}))(MuiDialogContent);

const DialogActions = withStyles((theme: Theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(1),
    borderTop: `1px solid ${fade("#000", 0.1)}`,
  },
}))(MuiDialogActions);

interface IMasAlertDialog {
  message?: string;
  description?: string;
  // children: React.ReactNode;
  onClose: () => void;
  handelConfirm: () => void;
  isOpen: boolean;
}

export default function MasAlertDialog(props: IMasAlertDialog) {
  const {message, description, onClose, isOpen, handelConfirm} = props;
  const classes = useStyles();
  return (
    <div>
      <Dialog
        classes={{
          paper: classes.dialog,
        }}
        open={isOpen}
        onClose={onClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title"> </DialogTitle>
        <DialogContent>
          {message ? (
            message
          ) : (
            <Typography className={classes.textHero} variant="h6">
                    
            </Typography>
          )}
          <div>
            {description ? (
              description
            ) : (
              <p className={classes.desc}>
                      ""     
                 .
              </p>
            )}
          </div>
        </DialogContent>
        <DialogActions>
          <Button
            disableElevation
            variant="contained"
            color="primary"
            onClick={handelConfirm}
          >
            
          </Button>
          <Button onClick={onClose} color="default" autoFocus>
            
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
