import React from "react";
import clsx from "clsx";
import {Bill} from "../proto/service_pb";
import {
	TableCell,
	Table,
	TableHead,
	TableRow,
	TableBody,
	Grid,
	makeStyles,
	withStyles,
	Box,
	fade,
} from "@material-ui/core";
import {timeStampToDate} from "../client/converter";

const useStyles = makeStyles((theme) => ({
	billHeader: {
		display: "flex",
		alignItems: "center",
		padding: "8px",
		gap: "6px",
		backgroundColor: fade(theme.palette.primary.main, 0.3),
		fontFamily: "DINBLD, sans-serif",
		flexDirection: "row",
		justifyContent: "space-between",
	},
	billNewHeader: {
		backgroundColor: fade(theme.palette.secondary.main, 0.3),
		color: theme.palette.text.primary,
		fontFamily: "DINBLD, sans-serif",
	},
	table: {
		border: "1px solid #ccc",
		backgroundColor: "#fff",
	},
	input: {
		width: "100px",
	},
	totalAmt: {
		fontWeight: "bold",
		padding: "0 12px",
		backgroundColor: "rgba(255,255,255,0.45)",
	},
}));

const StyledTableCell = withStyles(() => ({
	head: {
		fontFamily: "DINBLD, Arial , sans-serif",
		textAlign: "center",
		padding: "8px",
	},
	body: {
		fontSize: "1rem",
		border: "1px solid #d4d8dc",
		textAlign: "center",
		"&:last-child": {
			borderRightColor: "transparent",
		},
	},
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
	root: {
		"&:nth-of-type(even)": {
			backgroundColor: theme.palette.action.hover,
		},
	},
}))(TableRow);

export default function Bills({
	newBill,
	oldBill,
	calculated,
}: {
	newBill: Bill.AsObject;
	oldBill: Bill.AsObject;
	calculated: boolean;
}) {
	const classes = useStyles();
	let newAmt =
		Math.round(
			100 *
				newBill?.ftransactionsList.reduce((a, b) => {
					// if (b.code === "CRDT_AMT") {
					//   if (b.amount !== 0) {
					//     b.amount = Math.abs(b.amount) * -1;
					//   }
					// }
					return a + (b.amount || 0);
				}, 0)
		) / 100;

	return (
		<>
			<Grid container spacing={3} wrap="nowrap">
				<Grid item style={{flexGrow: 1}}>
					<Box className={classes.billHeader}>
						<Box>
							  -
							<span>{timeStampToDate(oldBill?.bilngdate)?.toLocaleDateString()}</span>
						</Box>
						<Box>
							 <span className={classes.totalAmt}>{oldBill?.clbalance}</span>
						</Box>
					</Box>
					<Table className={classes.table}>
						<TableHead>
							<StyledTableRow>
								<StyledTableCell></StyledTableCell>
								<StyledTableCell></StyledTableCell>
								<StyledTableCell></StyledTableCell>
								<StyledTableCell> </StyledTableCell>
								<StyledTableCell></StyledTableCell>
							</StyledTableRow>
						</TableHead>
						<TableBody>
							{(oldBill?.ftransactionsList || []).map((m, i) => {
								return (
									<StyledTableRow key={i}>
										<StyledTableCell>{m?.description || m?.code}</StyledTableCell>
										<StyledTableCell>{m?.amount}</StyledTableCell>
										<StyledTableCell>{m?.ctype?.description}</StyledTableCell>
										<StyledTableCell>{m?.nounits}</StyledTableCell>
										<StyledTableCell>{m?.mtransaction?.consump}</StyledTableCell>
									</StyledTableRow>
								);
							})}
						</TableBody>
					</Table>
				</Grid>
				{calculated && (
					<Grid item style={{flexGrow: 1}}>
						<Box className={clsx(classes.billHeader, classes.billNewHeader)}>
							<Box>
								  -
								<span>{timeStampToDate(newBill?.bilngdate).toLocaleDateString()}</span>
							</Box>
							<Box>
								 <span className={classes.totalAmt}>{newAmt}</span>
							</Box>
						</Box>
						<Table className={classes.table}>
							<TableHead>
								<StyledTableRow>
									<StyledTableCell></StyledTableCell>
									<StyledTableCell></StyledTableCell>
									<StyledTableCell></StyledTableCell>
									<StyledTableCell> </StyledTableCell>
									<StyledTableCell></StyledTableCell>
								</StyledTableRow>
							</TableHead>
							<TableBody>
								{(newBill?.ftransactionsList || []).map((m, i) => {
									return (
										<StyledTableRow key={i}>
											<StyledTableCell>{m?.description || m?.code}</StyledTableCell>
											<StyledTableCell>{m?.amount}</StyledTableCell>
											<StyledTableCell>{m?.ctype?.description}</StyledTableCell>
											<StyledTableCell>{m?.nounits}</StyledTableCell>
											<StyledTableCell>{m?.mtransaction?.consump}</StyledTableCell>
										</StyledTableRow>
									);
								})}
							</TableBody>
						</Table>
					</Grid>
				)}
			</Grid>
		</>
	);
}
