const width = "250px";

const useStyles = makeStyles((theme) => ({
	root: {
		width: "100%",
		height: "100%",
	},
	appContainer: {
		width: `calc(100% - ${theme.spacing(9)}px)`,
		height: "100%",
		//padding: theme.spacing(1),
		// overflow: "hidden",
		position: "relative",
		marginTop: theme.spacing(10),
		marginLeft: theme.spacing(9),
		transition: theme.transitions.create("all", {
			easing: theme.transitions.easing.sharp,
			duration: theme.transitions.duration.enteringScreen,
		}),
	},
	appContainerShift: {
		width: `calc(100% - ${width})`,
		marginLeft: `${width}`,
		transition: theme.transitions.create("all", {
			easing: theme.transitions.easing.sharp,
			duration: theme.transitions.duration.leavingScreen,
		}),
	},
	appHeader: {},
	title: {
		fontSize: "1.4rem",
		fontFamily: "DINMED",
	},
}));

export default function Layout() {
	const classes = useStyles();
	const isLogged = useSelector((a: RootStore) => a.login.isLoged);
	const [open, setOpen] = useState(false);
	const handleClicked = () => {
		setOpen((op) => !op);
	};
	let token = localStorage.getItem("token");

	if (!isLogged && token === null) {
		return (
			<>
				<LoginPage />
				<MasSnackbar />
			</>
		);
	} else {
		return (
			<>
				<MasLoading />
				<div className={classes.root}>
					<Header
						drawerWidth={width}
						titleStyle={classes.title}
						title=" "
						handleDrawerOpen={handleClicked}
						open={open}
					/>
					<main className={clsx(classes.appContainer, open && classes.appContainerShift)}>
						<MasDrawer
							drawerWidth={width}
							open={open}
							handleDrawerClose={handleClicked}
						/>
						<Routes />
					</main>
					<MasSnackbar />
				</div>
			</>
		);
	}
}

