import {createStore, compose, applyMiddleware} from "redux";
import rootReducers from "./reducers";
// import logger from "redux-logger";
import thunkMiddleware from "redux-thunk";
import {composeWithDevTools} from "redux-devtools-extension";
import {CLOSE_LOADING, SHOW_LOADING} from "./actions/common/loading";
import { AlertProps } from "@material-ui/lab/Alert";

declare global {
	interface Window {
		__REDUX_DEVTOOLS_EXTENSION_COMPOSE__?: typeof compose;
	}
}

const composedEnhancer = composeWithDevTools(applyMiddleware(thunkMiddleware));
export const store = createStore(rootReducers, composedEnhancer);

export type RootStore = ReturnType<typeof rootReducers>;

export const showLoading = () => {
	store.dispatch({
		type: SHOW_LOADING,
	});
};

export const hideLoading = () => {
	store.dispatch({
		type: CLOSE_LOADING,
	});
};

export const showNotification = (msg: string, severity: AlertProps["severity"]) => {
	store.dispatch({
		type: "SHOW_NOTIFICATION",
		payload: {
			msg: msg,
			severity: severity,
		},
	});
};

export default store;
