import {LoginResponce} from "../../proto/service_pb";
import {
	LoginDispatchTypes,
	LOGIN_FAIL,
	LOGIN_SUCCESS,
	LOG_OUT,
} from "../actions/login/loginTypes";

const user = localStorage.getItem("token");

const intialState =
	user !== null
		? {isLoged: true}
		: {
				isLoged: false,
				error: "",
		  };

export interface Istate {
	isLoged?: boolean;
	error?: string;
	user?: LoginResponce.AsObject;
}

export const LoginReducer = (state: Istate = intialState, action: LoginDispatchTypes) => {
	switch (action.type) {
		case LOGIN_SUCCESS:
			return {
				isLoged: true,
				user: action.payload,
			};
		case LOGIN_FAIL:
			return {
				isLoged: false,
				error: action.payload,
			};
		case LOG_OUT:
			return {
				isLoged: false,
			};
		default:
			return state;
	}
};
