import * as React from "react";
import {makeStyles} from "@material-ui/core/styles";
import {
	Grid,
	InputProps,
	Paper,
	fade,
	Select,
	MenuItem,
	Fab,
	Box,
	Typography,
} from "@material-ui/core";
import DateFnsUtils from "@date-io/date-fns";
import {MuiPickersUtilsProvider, KeyboardDatePicker} from "@material-ui/pickers";
import {GetBillsByCustkey, GetBillsByFormNo, GetLookup} from "../../client/grpc-client";
import {Bill, BillResponce, ENTITY_TYPE, LookUp} from "../../proto/service_pb";
import {MasTextField} from "../../components/ui/MasTextField";
import {MasButton} from "../../components/ui/MasButton";
import MasStepper from "./mas-stepper";
import ShowDialog from "../../components/show-dialog";
import {RecalcItem, UserData} from "./recalc-item-cls";
import {PostBills, SendRequest} from "../../redux/actions/common/prepear-request";
import {timeStampToDate} from "../../client/converter";
import {RootStore, showNotification} from "../../redux/store";
import {useDispatch, useSelector} from "react-redux";
import {getCtgs} from "../../redux/actions/ctgs/ctgsActions";
import CommentIcon from "@material-ui/icons/Comment";
// import MasTooltip from "../../components/ui/MasTooltip";

const searchByCustkey = 1;
const searchByFormNo = 2;

export interface Notification {
	sevirity?: string;
	open?: boolean;
	msg?: string;
}

export interface MyInputProps extends InputProps {
	itm: string;
	type?: string | "text";
}

export interface DataItem {
	oldBill: Bill.AsObject;
	newBill?: Bill.AsObject;
	userData?: UserData;
	calculated?: boolean;
	posted?: boolean;
	changed?: boolean;
}

export const RecalcPage = () => {
	const classes = useStyles();
	const [showCustKey, SetshowCustKey] = React.useState<boolean>(true);
	const [bilngDate, setBilngDate] = React.useState<Date>(new Date());
	const [key, setKey] = React.useState<string>("");
	const [response, setResponce] = React.useState<BillResponce.AsObject>(null);
	const [, setOpen] = React.useState<Notification>({});
	const [loading, setLoading] = React.useState<boolean>(false);
	const [meterDiameters, setMeterDiameters] = React.useState<Array<LookUp.AsObject>>([]);
	const [searchBy, setSearchBy] = React.useState<number>(searchByFormNo);
	const [isCancelledRequest, setIsCancelledRequest] = React.useState<boolean>(false);
	const [formNo, setFormNo] = React.useState<number>(null);
	const [billsData, setBillsData] = React.useState<Array<DataItem>>([]);
	const [billsDataForPreview, setBillsDataForPreview] = React.useState<Array<DataItem>>(
		[]
	);
	const [showPreview, setShowPreview] = React.useState<boolean>(false);
	const [isPreview, setIsPreview] = React.useState<boolean>(false);
	const [isCalacAll, setIsCalacAll] = React.useState<boolean>(false);
	const [showComment, setShowComment] = React.useState<boolean>(false);

	const dispatch = useDispatch();
	const ctgs = useSelector((state: RootStore) => state.Ctgs.ctgs);

	const handleDateChange = (date) => {
		setBilngDate(date);
	};
	const handleCustkeyChange = (ck: any) => {
		setKey(ck.target.value);
	};
	const showErrorMessage = (err, status?: string) => {
		let msg = "";
		if (typeof err == "object") {
			if (err.toString) {
				msg = err.toString();
			} else {
				msg = JSON.stringify(err);
			}
		} else {
			msg = err;
		}
		setOpen({open: true, msg, sevirity: status});
	};
	const loadMeterDiameters = () => {
		GetLookup(ENTITY_TYPE.METER_DIAMETER)
			.then((d) => {
				setMeterDiameters(d);
			})
			.catch((er) => {
				showErrorMessage(er, "error");
			});
	};
	const handleGetBill = () => {
		setBillsData([]);
		if (!key) {
			alert("   ");
			return;
		}
		if (searchBy === searchByCustkey) {
			setIsCancelledRequest(false);
			setFormNo(null);
			GetBillsByCustkey(key, bilngDate)
				.then((d) => {
					setResponce(d);
					let bils = (d.billsList || []).map((a) => {
						let di: DataItem = {
							oldBill: a,
							newBill: {...a},
							userData: {},
						};
						return di;
					});
					setBillsData([...bils]);
				})
				.catch((ex) => {
					alert(ex);
				});
		} else {
			setIsCancelledRequest(true);
			if (Number(key) === null) {
				alert("   ");
				return;
			}
			setFormNo(Number(key));
			GetBillsByFormNo(key)
				.then((d) => {
					setLoading(false);
					setResponce(d);
					console.log(d, "customer");
					let bils = (d.billsList || []).map((a) => {
						let di: DataItem = {
							oldBill: a,
							newBill: {...a},
							userData: {},
						};
						return di;
					});
					setBillsData([...bils]);
				})
				.catch((e) => {
					let err = e.Message || e.message || e.error || e.Exception || JSON.stringify(e);
					setLoading(false);
					showNotification(err, "error");
				});
		}
	};
	const handleRecalItemChargeRquest = (billIndex: number, userData: UserData) => {
		let itm = billsData[billIndex];
		console.log(userData.MainCtype, "from charge ");
		SendRequest({
			cycleLength: 1,
			bilngDate: timeStampToDate(itm.newBill.bilngdate),
			userData: userData,
			newBill: itm.newBill,
			oldBill: itm.oldBill,
		})
			.then((d) => {
				itm.newBill = d;
				itm.calculated = true;
				setBillsData([...billsData]);
				showNotification("", "success");
			})
			.catch((e) => {
				let err = e.Message || e.message || e.error || e.Exception || JSON.stringify(e);
				showNotification(err, "error");
			});
	};
	const handleUserDataChange = (billIndex: number, userdata: UserData) => {
		let itm = billsData[billIndex];
		itm.userData = {...itm.userData, ...userdata};
		itm.changed = true;
		setBillsData(billsData);
	};
	const handleApplyToAll = (billIndex: number, userdata: UserData) => {
		if (billsData && billsData?.length > 0 && userdata) {
			let readType = Number(userdata.ReadType);
			let prRead = Number(userdata.PrReading);
			let crRead = Number(userdata.CrReading);
			let consump = crRead - prRead;
			if (consump < 0) {
				consump = 0;
			}
			let nwBillsData = billsData.map((b, idx) => {
				let pr = prRead + idx * consump;
				let cr = crRead + idx * consump;
				if (idx === billIndex) {
					return b;
				}
				b.userData = {...b.userData, ...userdata};
				if (readType === 0 && billIndex === 0) {
					b.userData.Consumption = consump;
					b.userData.PrReading = pr;
					b.userData.CrReading = cr;
					b.userData.CrDate = null;
					b.userData.PrDate = null;
				}
				return b;
			});
			setBillsData(nwBillsData);
			setIsCalacAll(false);
		}
	};
	const handleRecalcItemPreview = (billIndex: number, userdata: UserData) => {
		setBillsDataForPreview([billsData[billIndex]]);
		setShowPreview(true);
	};
	const handleAllPreview = () => {
		if (billsData && billsData?.length > 0) {
			setBillsDataForPreview(billsData);
			setShowPreview(true);
			setIsPreview(true);
		}
	};
	const handleAllCharge = () => {
		setIsPreview(false);
		setIsCalacAll(false);
		if (billsData && billsData?.length > 0) {
			let tasks = billsData.map((itm) => {
				console.log(itm, "from handleAllCharge");
				return SendRequest({
					cycleLength: 1,
					bilngDate: timeStampToDate(itm.newBill.bilngdate),
					userData: itm.userData,
					newBill: itm.newBill,
					oldBill: itm.oldBill,
				});
			});
			Promise.all(tasks)
				.then((rs) => {
					(rs || []).forEach((itm, idx) => {
						//billsData[idx].newBill=itm;
						billsData[idx] = {...billsData[idx], newBill: itm, calculated: true};
					});
					setBillsData([...billsData]);
					showNotification("", "success");

					setIsCalacAll(true);
				})
				.catch((e) => {
					let err = e.Message || e.message || e.error || e.Exception || JSON.stringify(e);
					showNotification(err, "error");
				});
		}
	};
	const handlePost = () => {
		if (!isCalacAll) {
			showNotification("    ", "error");
			return;
		}
		if (!isPreview) {
			showNotification("     ", "error");
			return;
		}
		if (billsData && billsData?.length > 0) {
			let bills = billsData.map((itm) => {
				return itm.newBill;
			});
			PostBills(bills, isCancelledRequest, formNo)
				.then(() => {
					showNotification(" ", "success");
				})
				.catch((e) => {
					let err = e.Message || e.message || e.error || e.Exception || JSON.stringify(e);
					showNotification(err, "error");
				});
		}
	};
	const handelComment = () => {
		setShowComment(!showComment);
	};
	React.useEffect(() => {
		loadMeterDiameters();
		dispatch(getCtgs());
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, []);
	React.useEffect(() => {
		console.log(billsData, "billsData");
	}, [showComment, billsData]);
	return (
		<MuiPickersUtilsProvider utils={DateFnsUtils}>
			{/* <Grid>{JSON.stringify(userData)}</Grid> */}
			<div className={classes.root}>
				<Paper className={classes.header}>
					<Grid container direction="row" alignItems="center" spacing={1} wrap="nowrap">
						<Grid item>
							<Select
								variant="outlined"
								color="secondary"
								className={classes.inputSearch}
								value={searchBy}
								onChange={(evt) => {
									setSearchBy(evt.target.value as any);
									SetshowCustKey(!showCustKey);
								}}
							>
								<MenuItem value={searchByFormNo}>  </MenuItem>
								<MenuItem value={searchByCustkey}>  </MenuItem>
							</Select>
						</Grid>

						<Grid item xs={12}>
							<MasTextField
								required={false}
								fullWidth
								variant="outlined"
								onChange={handleCustkeyChange}
								label={searchBy === searchByCustkey ? " " : " "}
								value={key}
								type="number"
							/>
						</Grid>
						{searchBy === searchByCustkey && (
							<Grid item xs={12}>
								<KeyboardDatePicker
									disableFuture
									autoOk
									openTo="year"
									views={["year", "month", "date"]}
									fullWidth={true}
									variant="inline"
									inputVariant="outlined"
									format="yyyy/MM/dd"
									id="date-picker-inline"
									label="  "
									value={bilngDate}
									onChange={handleDateChange}
									KeyboardButtonProps={{
										"aria-label": "change date",
									}}
								/>
							</Grid>
						)}
						<Grid item>
							<Grid container direction="column" alignContent="space-around">
								<MasButton
									type="button"
									disableElevation={true}
									variant="contained"
									label=""
									onClick={handleGetBill}
									color="primary"
								/>
							</Grid>
						</Grid>
					</Grid>
				</Paper>
				{billsData?.length > 0 && (
					<Paper elevation={0} className={classes.btnAll}>
						<Grid container spacing={1} justify="space-between">
							<Grid item container direction="row" xs={8}>
								<Grid item>
									<MasButton
										type="button"
										disableElevation={true}
										variant="contained"
										label=" "
										onClick={handleAllCharge}
										color="primary"
									/>
								</Grid>
								<Grid item>
									<MasButton
										type="button"
										disableElevation={true}
										variant="outlined"
										label=" "
										onClick={handleAllPreview}
										color="secondary"
									/>
								</Grid>
								<Grid item>
									<MasButton
										type="button"
										disableElevation={true}
										variant="contained"
										label=" "
										onClick={handlePost}
										color="secondary"
									/>
								</Grid>
							</Grid>
						</Grid>
					</Paper>
				)}
				{!loading &&
					(billsData || []).map((bill, idx) => {
						return (
							<RecalcItem
								isCancelledRequest={isCancelledRequest}
								formNo={formNo}
								ctgs={ctgs}
								meterDiameters={meterDiameters}
								key={idx}
								oldBill={bill.oldBill}
								newBill={bill.newBill}
								billIndex={idx}
								initalUserData={bill.userData}
								onUserDataChange={handleUserDataChange}
								onPreviewClick={handleRecalcItemPreview}
								onChargeClick={handleRecalItemChargeRquest}
								onApplyToAll={handleApplyToAll}
								showCustKey={showCustKey}
							/>
						);
					})}
				{searchBy === searchByFormNo && billsData?.length > 0 && (
					<Box className={classes.fabBtn}>
						<Fab onClick={handelComment} size="medium" aria-label="add" color="primary">
							<CommentIcon
								style={{
									color: "#fff",
								}}
							/>
						</Fab>
						<Box
							className={classes.fabContent}
							style={{
								opacity: showComment ? 100 : 0,
							}}
						>
							<Typography variant="body2">
								{response?.recalcform?.comment === undefined || ""
									? "    "
									: response?.recalcform?.comment}
							</Typography>
						</Box>
					</Box>
				)}
				<ShowDialog
					onClose={() => {
						setShowPreview(false);
					}}
					isOpen={showPreview || false}
					children={
						<MasStepper
							onConfirm={(bill) => {
								return Promise.resolve();
							}}
							bills={billsDataForPreview}
							executBill={false}
						></MasStepper>
					}
				/>
			</div>
		</MuiPickersUtilsProvider>
	);
};

const useStyles = makeStyles((theme) => ({
	root: {
		display: "flex",
		flexDirection: "column",
		alignItems: "center",
		gap: "16px",
		padding: "16px",
		position: "relative",
	},
	header: {
		display: "flex",
		gap: 12,
		justifyContent: "space-between",
		alignItems: "center",
		backgroundColor: "#ffff",
		padding: "16px",
		width: "100%",
		[theme.breakpoints.down("md")]: {
			// maxWidth: "90%",
		},
		[theme.breakpoints.down("xs")]: {
			// maxWidth: "100%",
		},
	},
	table: {
		minWidth: 650,
	},
	darkColum: {
		backgroundColor: "#fafafa",
		border: "1px solid gray",
	},
	oddRow: {
		backgroundColor: "#f0f0f0",
	},
	evenRow: {},
	button: {
		backgroundColor: "gray",
	},
	newBill: {
		backgroundColor: "#f1f1f1",
	},
	oldBill: {},
	formControl: {
		width: "100%",
	},
	headline: {
		writingMode: "vertical-rl",
		transform: " rotate(180deg)",
		fontWeight: 400,
	},
	headCell: {
		backgroundColor: fade(theme.palette.primary.main, 0.3),
		textAlign: "center",
		padding: "4px",
	},
	headCell2: {
		backgroundColor: fade(theme.palette.secondary.main, 0.3),
		textAlign: "center",
		padding: "4px",
	},
	btns: {
		padding: 16,
	},
	inputSearch: {
		backgroundColor: "#e5fdff",
	},
	btnAll: {
		marginBottom: -8,
		width: "100%",
		padding: theme.spacing(1, 2),
		// backgroundColor:fade(theme.palette.secondary.main, 0.3) ,
		backgroundColor: "#2c4b61",
		borderRadius: 0,
		position: "sticky",
		top: "0",
		boxShadow: "0 6px 8px rgba(0,0,0,0.15)",
		zIndex: 1000,
	},
	fabBtn: {
		position: "fixed",
		bottom: 30,
		right: 20,
	},
	fabContent: {
		position: "absolute",
		minWidth: "230px",
		background: "#fff",
		borderRadius: 10,
		boxShadow: "0 0 8px rgba(0,0,0,0.15)",
		bottom: 55,
		right: 20,
		color: "#ebf9ff",
		border: "2px solid #fff",
		backgroundColor: "#2c4b61",
		padding: theme.spacing(1.5),
		transition: "opacity 0.3s ease-out",
		textAlign: "center",
	},
}));
