import React from "react";
import {useSelector} from "react-redux";
import {Switch, Route, Redirect} from "react-router-dom";
import * as pages from "../pages/index";
import {RecalcPage} from "../pages/recalc/recalc";
import {RootStore} from "../redux/store";

function NotFound() {
	return <>You have landed on a page that doesn't exist</>;
}

export default function Routes() {
	let loggedIn = useSelector((state: RootStore) => state.login.isLoged);
	return (
		<Switch>
			<Route exact path="/" component={pages.Home}></Route>
			<Route
				exact
				path="/"
				render={() => {
					return loggedIn ? <Redirect to="/home" /> : <Redirect to="/login" />;
				}}
			/>
			<Route path="/recalc" component={RecalcPage}></Route>
			<Route path="/cancellBills" component={pages.CancellBills}></Route>
			<Route path="/cancellBillsRequest" component={pages.CancellBillsRequest}></Route>
			<Route path="/cancellBillsAction" component={pages.CancellBillsAction}></Route>
			<Route path="/notfound" component={NotFound}></Route>
			<Redirect to="/notfound" />
		</Switch>
	);
}
