import {createStyles} from "@material-ui/core";
import DINBLD from "../assets/fonts/DinNextBold.woff";
import DINREG from "../assets/fonts/DinNextRegular.woff";
import DINMED from "../assets/fonts/DinNextMedium.woff";
import theme from "./theme";

const globalStyles = createStyles(() => ({
	"@global": {
		"@font-face": [
			{
				fontFamily: "DINREG",
				fontStyle: "normal",
				fontWeight: "normal",
				src: `url(${DINREG}) format('woff')`,
			},
			{
				fontFamily: "DINMED",
				fontStyle: "normal",
				fontWeight: "normal",
				src: `url(${DINMED}) format('woff')`,
			},
			{
				fontFamily: "DINBLD",
				fontStyle: "normal",
				fontWeight: "normal",
				src: `url(${DINBLD}) format('woff')`,
			},
		],
		"*": {
			boxSizing: "border-box",
			margin: "0",
			padding: "0",
			fontFamily: "DINREG",
		},
		a: {
			textDecoration: "none",
			color: "inherit",
		},
		body: {
			scrollBehavior: "smooth",
			background: "#F0F7F7",
			overflowX: "hidden",
			// minHeight: "100vh",
		},
		".flexgrow": {
			flexGrow: 1,
		},
		".text-left": {
			textAlign: "left",
		},
		".text-right": {
			textAlign: "right",
		},
		".text-center": {
			textAlign: "center",
		},
		".bold": {
			fontFamily: "DINBLD",
			color: theme.palette.text.primary,
		},
		".inline": {
			display: "inline",
		},
	},
}));

export default globalStyles;
