import {Ctg} from "../../proto/service_pb";
import {CtgsDispatchTypes, CTGS_FAIL, CTGS_SUCSSES} from "../actions/ctgs/ctgsTypes";

interface Istate {
	ctgs?: Array<Ctg.AsObject>;
	err?: string;
}

const intialState = {
	ctgs: [],
};

export const CtgsReducser = (state: Istate = intialState, actions: CtgsDispatchTypes) => {
	switch (actions.type) {
		case CTGS_SUCSSES:
			return {
				ctgs: actions.payload,
			};
		case CTGS_FAIL:
			return {
				err: actions.payload,
			};
		default:
			return state;
	}
};
