import {createMuiTheme} from "@material-ui/core";

const primary = "#00a1a1", // 006666
	secondary = "#FF836B",
	third = "#FFBC00",
	primaryLight = "#DDF3F3",
	secondaryLight = "#FFCFC6",
	white = "#ffffff",
	background = "#0d0c22",
	textBlack = "#191B1B",
	textLight = "#889797",
	lightGray = "#EBEBEB";
	
const theme = createMuiTheme({
	palette: {
		primary: {
			main: primary,
			contrastText: white,
			light: primaryLight,
		},
		secondary: {
			main: secondary,
			contrastText: white,
			light: secondaryLight,
		},
		text: {
			primary: textBlack,
			secondary: textLight,
		},
		background: {
			default: background,
		},
		common: {
			black: "#424242",
		},
		third: {
			main: third,
			contrastText: textBlack,
		},
	},
	shadows: [
		"none",
		"0px 2px 1px -1px rgba(0,0,0,0.2),0px 1px 1px 0px rgba(0,0,0,0.14),0px 1px 3px 0px rgba(0,0,0,0.12)",
		"0px 3px 1px -2px rgba(0,0,0,0.2),0px 2px 2px 0px rgba(0,0,0,0.14),0px 1px 5px 0px rgba(0,0,0,0.12)",
		"0px 3px 3px -2px rgba(0,0,0,0.2),0px 3px 4px 0px rgba(0,0,0,0.14),0px 1px 8px 0px rgba(0,0,0,0.12)",
		"0 1px 12px 0 rgba(46, 61, 73, 0.15)", //4
		"0 1px 20px 0 rgba(46, 61, 73, 0.2)", //5
		"0px 3px 5px -1px rgba(0,0,0,0.2),0px 6px 10px 0px rgba(0,0,0,0.14),0px 1px 18px 0px rgba(0,0,0,0.12)",
		"0px 4px 5px -2px rgba(0,0,0,0.2),0px 7px 10px 1px rgba(0,0,0,0.14),0px 2px 16px 1px rgba(0,0,0,0.12)",
		"0px 5px 5px -3px rgba(0,0,0,0.2),0px 8px 10px 1px rgba(0,0,0,0.14),0px 3px 14px 2px rgba(0,0,0,0.12)",
		"0px 5px 6px -3px rgba(0,0,0,0.2),0px 9px 12px 1px rgba(0,0,0,0.14),0px 3px 16px 2px rgba(0,0,0,0.12)",
		"0px 6px 6px -3px rgba(0,0,0,0.2),0px 10px 14px 1px rgba(0,0,0,0.14),0px 4px 18px 3px rgba(0,0,0,0.12)",
		"0px 6px 7px -4px rgba(0,0,0,0.2),0px 11px 15px 1px rgba(0,0,0,0.14),0px 4px 20px 3px rgba(0,0,0,0.12)",
	],
	typography: {
		fontFamily: "inherit",
	},
	direction: "rtl",
	overrides: {
		MuiCssBaseline: {
			"@global": {
				html: {
					WebkitFontSmoothing: "auto",
				},
				body: {
					backgroundColor: "#fff",
					fontFamily: "DINREG ,  Helvetica, Arial, sans-serif",
				},
			},
		},
		MuiInputBase: {
			root: {
				fontSize: "1.2rem",
				"@media (min-width: 1700px)": {
					fontSize: "1.2rem",
				},
				"&::before": {
					border: "none",
				},
			},
		},
		MuiInputLabel: {
			outlinedSecondary: {},
			filled: {},
		},
		MuiOutlinedInput: {
			input: {},
		},
		MuiFilledInput: {
			root: {
				backgroundColor: lightGray,
				color: textBlack,
				"&:hover": {
					backgroundColor: "none",
				},
			},
		},
		MuiButton: {
			outlinedSecondary: {
				borderColor: "#c6c8ca",
			},
		},
		MuiTab: {
			root: {
				border: "1px solid rgb(0 157 157 / 20%)",
				"&:hover": {
					backgroundColor: "rgb(0 225 235 / 20%)",
				},
				"&.Mui-selected": {
					transition: "opacity .1s ease",
					fontFamily: "DINBLD",
					color: "#fff",
					background: primary,
					borderRadius: "4px",
					border: 0,
				},
			},
			textColorInherit: {
				opacity: 0.6,
			},
		},
		MuiTabs: {
			indicator: {
				display: "flex",
				justifyContent: "center",
				backgroundColor: "transparent",
				bottom: "5px",
				"& > span": {
					maxWidth: 50,
					width: "100%",
				},
			},
		},
		MuiTableRow: {
			root: {
				"&.Mui-selected, Mui-selected:hover": {
					backgroundColor: "rgb(218 223 230) !important",
				},
			},
		},
		MuiTreeItem: {
			root: {
				"&.Mui-selected > .MuiTreeItem-content .MuiTreeItem-label": {
					backgroundColor: "#c6e1f6 !important",
				},
			},
		},
		MuiCheckbox: {
			root: {
				color: "inherit",
				"&.Mui-checked ": {
					color: "#007eff",
				},
			},
		},
		MuiChip: {
			root: {
				backgroundColor: "#fff",
				border: "1px solid #ddd",
				fontSize: "14px",
				fontWeight: "bold",
				fontFamily: "Helvetica-Bold, sans-serif",
			},
		},
		MuiAutocomplete: {
			inputRoot: {
				// paddingTop: "22px !important",
			},
		},
	},
	props: {
		MuiIconButton: {
			disableRipple: true,
		},
	},
});

export default theme;
